<template>
  <div class="d-flex align-center mt-3">
    <v-text-field
      :value="bundle.adName"
      :label="$t('common.adName')"
      outlined
      dense
      :error-messages="hasValidationInvalid('adName') ? $t('common.required') : null"
      hide-details="auto"
      @input="setValue('adName', $event)"
    />
    <v-btn
      color="primary"
      class="ml-3"
      @click="$store.dispatch('tiktokCreateAds/openDialog', 'adNameMacros')"
    >
      {{ $t('common.macro') }}
    </v-btn>
  </div>
</template>

<script>
import validationMixin from '@/mixins/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsName',

  mixins: [ validationMixin ],

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle'
    })
  },

  methods: {
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },
  }
};
</script>