<template>
  <div style="padding: 8px;">
    <dialogs-bulk-delete v-if="dialogs.bulkDelete" />
    <topbar />
    <main-table ref="mainTable" />
  </div>
</template>

<script>
import MainTable         from '@/components/tiktok/createAds/jobs/TiktokJobsMainTable.vue';
import Topbar            from '@/components/tiktok/createAds/jobs/TiktokJobsTopbar.vue';
import DialogsBulkDelete from '@/components/tiktok/createAds/jobs/dialogs/TiktokCreateAdsJobsDialogsBulkDelete.vue';
import { mapGetters }    from 'vuex';

export default {
  name: 'TiktokCreateAdsJobs',

  components: {
    MainTable,
    Topbar,
    DialogsBulkDelete
  },

  data() {
    return {
      updateInterval: null,
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokCreateAdsWizardJobs/dialogs',
    }),
  },

  created() {
    this.loadJobs();
    this.updateInterval = setInterval(() => {
      this.loadJobs();
    }, 60000);
  },

  destroyed() {
    clearInterval(this.updateInterval);
  },

  methods: {
    async loadJobs() {
      await this.$store.dispatch('tiktokCreateAdsWizardJobs/loadMainTableData', { endpoint: 'tiktok/create-ads/jobs' });
      this.$refs.mainTable.setStatusesInterval();
    }
  }
};
</script>