<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.bundles.mainDialog.cardsTitles.common') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <!--   -->
        <div class="d-flex align-center mt-3">
          <v-text-field
            :value="bundle.adsetName"
            :label="$t('common.adsetName')"
            outlined
            dense
            :error-messages="hasValidationInvalid('adsetName') ? $t('common.required') : null"
            hide-details="auto"
            @input="setAdsetName"
          />
          <v-btn
            color="primary"
            class="ml-3"
            @click="$store.dispatch('tiktokCreateAds/openDialog', 'adsetNameMacros')"
          >
            {{ $t('common.macro') }}
          </v-btn>
        </div>
      <!--    -->
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import validationMixin from '@/mixins/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsCommon',

  mixins: [ validationMixin ],

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle'
    })
  },

  methods: {
    setAdsetName(name) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'adsetName',
        value: name
      });
    }
  }
};
</script>