<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.bundles.mainDialog.cardsTitles.common') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <!--  -->
        <v-autocomplete
          :value="selectedBundleId"
          :label="$t('upload.wizard.static.bundle')"
          outlined
          dense
          :error-messages="hasSingleValidationInvalid('selectedBundle') ? $t('common.required') : null"
          hide-details="auto"
          :item-text="item => item.name"
          :item-value="item => item.id"
          :items="mainTableData"
          clearable
          return-object
          :loading="mainTableLoading"
          @input="setSelectedBundle"
        >
          <template #append-outer>
            <v-btn
              small
              color="primary"
              text
              block
              style="margin-top: -3px;"
              @click="openMainDialog"
            >
              <v-icon :size="18">
                add
              </v-icon>
              {{ $t('upload.bundles.topbar.createBundle') }}
            </v-btn>
          </template>
        </v-autocomplete>
        <!--   -->

        <div 
          v-if="uploadBundle"
          class="d-flex align-center mt-3"
        >
          <!--   -->
          <v-text-field
            :value="uploadBundle.campaignsQuantity"
            :label="$t('upload.wizard.static.quantityCampaigns')"
            type="number"
            min="1"
            max="10"
            outlined
            dense
            hide-details
            class="mr-2"
            @input="setValue('campaignsQuantity', $event)"
            @blur="onBlur('campaignsQuantity')"
          />
          <!--    -->

          <!--   -->
          <v-text-field
            :value="uploadBundle.adsetsQuantity"
            :label="$t('upload.wizard.static.quantityAdsets')"
            type="number"
            min="1"
            max="10"
            outlined
            dense
            hide-details
            class="ml-2"
            @input="setValue('adsetsQuantity', $event)"
            @blur="onBlur('adsetsQuantity')"
          />
          <!--    -->
        </div>
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import emptyUploadBundle from '@/constants/tiktok/upload/empty_upload_bundle';
import validationMixin   from '@/mixins/tiktok/common/validation_mixin';
import { mapFields }     from 'vuex-map-fields';
import { mapGetters }    from 'vuex';

export default {
  name: 'TiktokWizardSettingsCommon',

  mixins: [ validationMixin ],

  computed: {
    ...mapGetters({
      selectedBundle: 'tiktokCreateAdsWizard/selectedBundle',
      uploadBundle: 'tiktokCreateAdsWizard/uploadBundle',
      mainTableData: 'tiktokCreateAds/mainTableData',
      mainTableLoading: 'tiktokCreateAds/mainTableLoading',
      $validation: 'tiktokCreateAdsWizard/$validation',
      adAccounts: 'tiktokAdsmanagerAdAccounts/selected',
      adAccountsMainTableData: 'tiktokCreateAdsWizard/mainTableData',
    }),
    ...mapFields('tiktokCreateAdsWizard', [
      'selectedAdAccounts',
      'adAccountOptions',
      'filters'
    ]),

    selectedBundleId() {
      if (this.selectedBundle) {
        return this.selectedBundle.id;
      }
      return null;
    }
  },

  async created() {
    if (this.$route.query.name) {
      this.setSelectedBundle(this.mainTableData.find(bundle => bundle.name === this.$route.query.name));
    } else {
      this.setSelectedBundle();
    }
  },

  methods: {
    async openMainDialog() {
      await this.$store.dispatch('tiktokCreateAds/setBundle');
      this.$store.dispatch('tiktokCreateAds/openDialog', 'mainDialog');
    },

    async setSelectedBundle(bundle) {
      this.selectedAdAccounts = [];
      this.adAccountOptions = {};
      
      let selectedBundle = null;
      let uploadBundle = null;

      if (bundle) {
        selectedBundle = bundle;
        uploadBundle = this.$_.cloneDeep(emptyUploadBundle);
      }
      
      this.$store.dispatch('tiktokCreateAdsWizard/setSelectedBundle', selectedBundle);
      this.$store.dispatch('tiktokCreateAdsWizard/setUploadBundle', uploadBundle);

      const { adAccounts } = this.$route.query;
      if (adAccounts && this.selectedBundle) {
        this.setAdAccountsFromRouter(adAccounts);
      }
    },

    async setAdAccountsFromRouter(adAccounts) {
      this.filters.onlySelected = true;

      if (this.adAccountsMainTableData.length === 0) {
        await this.$store.dispatch('tiktokCreateAdsWizard/loadMainTableData', { endpoint: 'tiktok/adaccounts' });
      }

      const adAccountsArr =  adAccounts.split(',');
      for (const adAccountId of adAccountsArr) {
        const adAccount = this.adAccountsMainTableData.find(i => i.id === adAccountId);
        if (adAccount) {
          await this.$store.dispatch('tiktokCreateAdsWizard/addAdAccountOption', { id: adAccountId, adAccount });
          this.selectedAdAccounts.push(adAccount);
        }
      }
    },

    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAdsWizard/setUploadBundleProperty', { key, value });
    },

    onBlur(component) {
      if (this.uploadBundle[component] < 1) {
        this.setValue(component, '1');
      } else if (this.uploadBundle[component] > 10) {
        this.setValue(component, '10');
      }
    },
  }
};
</script>
