<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.bundles.mainDialog.cardsTitles.demography') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <!--  -->
        <div class="d-flex align-center">
          <span>{{ $t('common.age') }}</span>
          <v-btn-toggle
            :value="bundle.age"
            borderless
            color="primary"
            mandatory
            multiple
            class="ml-2"
            @change="setAge"
          >
            <v-btn
              v-for="(age, ageIndex) in ages"
              :key="`create-ads-main-dialog-demograpy-age-${age}-${ageIndex}`"
              :value="age" 
              style="font-size: 12px; height: 24px;"
            >
              <span>{{ age === 'all' ? $t('common.all') : age }}</span>
            </v-btn>
          </v-btn-toggle>
        </div>
        <!--   -->

        <!--  -->
        <div class="mt-3 d-flex align-center">
          <span>{{ $t('common.gender') }}</span>
          <v-btn-toggle
            :value="bundle.gender"
            borderless
            color="primary"
            mandatory
            class="ml-2"
            @change="setValue('gender', $event)"
          >
            <v-btn
              v-for="(gender, genderIndex) in genders"
              :key="`create-ads-main-dialog-demograpy-gender-${gender}-${genderIndex}`"
              :value="gender" 
              style="font-size: 12px; height: 24px;"
            >
              <span>{{ $t(`common.gender${capitalize(gender)}`) }}</span>
            </v-btn>
          </v-btn-toggle>
        </div>
        <!--   -->
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsDemography',

  data() {
    return {
      genders: [
        'all',
        'male',
        'female'
      ],
      ages: [
        'all',
        '13-17',
        '18-24',
        '25-34',
        '35-44',
        '45-54',
        '55+',
      ]
    };
  },

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle'
    })
  },

  methods: {
    setAdsetName(name) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'adsetName',
        value: name
      });
    },

    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },

    setAge(value) {
      const lastItem = Array.isArray(value) && value.length ? value[value.length - 1] : null;

      if (lastItem && lastItem === 'all') {
        this.setValue('age', ['all']);
      } else if (value.length === 6) {
        this.setValue('age', ['all']);
      } else {
        this.setValue('age', value.filter(i => i !== 'all'));
      }
    },

    capitalize(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    }
  }
};
</script>