<template>
  <v-col 
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  > 
    <v-row>
      <v-col
        v-for="(objective, objectiveIndex) in objectives"
        :key="`main-dialog-stepper-campaigns-objectives-${objective}-${objectiveIndex}`"
        :cols="12"
        :sm="6"
      >
        <v-chip
          class="create-ads-main-dialog-objective-chip"
          large
          label
          :color="bundle.objective === objective.toUpperCase() ? 'primary' : 'default'"
          @click="setObjective(objective)"
        > 
          {{ $t(`upload.objectives.${objective}`) }}
        </v-chip>
      </v-col>
    </v-row>
  </v-col>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperCampaignsObjectives',

  data: () => ({
    objectives: [
      'app_installs',
      'conversions',
      'traffic',
      // 'reach'
    ],
  }),

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle'
    })
  },

  methods: {
    setObjective(objective) {
      if (objective === 'reach') {
        this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
          key: 'budgetLevel',
          value: 'adset'
        });
      }

      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'objective',
        value: objective.toUpperCase()
      });
    }
  }
};
</script>
