<template>
  <v-dialog
    :value="dialogs.campaignNameMacros"
    width="800"
    :transition="false"
    scrollable
    @keydown.esc="closeDialog"
    @click:outside="closeDialog"
  >
    <v-card>
      <v-card-title>{{ $t('common.availableMacrosForCampaigns') }}</v-card-title>
      <v-card-text>
        <v-text-field
          v-model="campaignName"
          :label="$t('common.campaignName')"
          outlined
          dense
          hide-details
          class="my-2"
        />
        <div
          v-for="item in campaignMacros"
          :key="`campaings-macro-dialog-${item.macro}`"
        >
          <v-btn
            class="mb-1"
            x-small
            outlined
            :color="campaignName.includes(item.macro) ? 'primary' : 'grey'"
            @click="addMacro(item.macro)"
          >
            {{ item.macro }}
          </v-btn>
          <span>{{ ' - ' + $t(`upload.bundles.mainDialog.macros.${item.locale}`) }}</span>
        </div>
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <!--  -->
        <v-btn
          text
          color="grey"
          @click="closeDialog"
        >
          {{ $t('common.cancel') }}
        </v-btn>
        <!--   -->

        <!--  -->
        <v-btn
          color="primary"
          text
          @click="save"
        >
          {{ $t('common.save') }}
        </v-btn>
        <!--   -->
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { campaignMacros } from '@/constants/tiktok/upload/macros';
import { mapGetters }     from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperCampaignsDialogNameMacros',

  data() {
    return {
      campaignName: '',
      campaignMacros
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokCreateAds/dialogs',
      bundle: 'tiktokCreateAds/bundle'
    }),
  },

  created() {
    if (this.bundle.campaignName) {
      this.campaignName = this.bundle.campaignName;
    }
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokCreateAds/closeDialog', 'campaignNameMacros');
    },

    addMacro(macro) {
      if (this.campaignName.includes(macro)) {
        this.campaignName = this.campaignName.replace(macro, '');
      } else {
        this.campaignName += macro;
      }
    },

    save() {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'campaignName',
        value: this.campaignName
      });
      this.closeDialog();
    }
  }
};
</script>