<template>
  <v-dialog
    :value="dialogs.mainDialog"
    persistent
    fullscreen
    :transition="false"
    content-class="create-ads-main-dialog"
    @keydown.esc="$store.dispatch('tiktokCreateAds/closeDialog', 'mainDialog')"
  >
    <dialog-campaign-name-macros v-if="dialogs.campaignNameMacros" />
    <dialog-about-budget v-if="dialogs.aboutBudget" />
    <dialog-budget-randomize v-if="dialogs.budgetRandomize" />
    <dialog-adset-name-macros v-if="dialogs.adsetNameMacros" />
    <dialog-ad-name-macros v-if="dialogs.adNameMacros" />
    <dialog-available-spintax v-if="dialogs.availableSpintax" />

    <v-card>
      <!--   -->
      <main-dialog-header ref="mainDialogHeader" />
      <!--   -->

      <!--  -->
      <main-dialog-stepper />
      <!--  -->

      <!--   -->
      <v-btn
        v-if="bundle.step !== 3"
        class="create-ads-main-dialog-main-button"
        color="primary"
        @click="handleMainButton('continue')"
      >
        {{ $t('common.continue') }}
      </v-btn>
      <!--    -->

      <!--  -->
      <v-btn
        v-else-if="bundle.id"
        class="create-ads-main-dialog-main-button"
        color="primary"
        @click="handleMainButton('edit')"
      >
        {{ $t('common.edit') }}
      </v-btn>
      <v-btn
        v-else
        class="create-ads-main-dialog-main-button"
        color="primary"
        @click="handleMainButton('create')"
      >
        {{ $t('common.create') }}
      </v-btn>
      <!--   -->
    </v-card>
  </v-dialog>
</template>
<script>
import { mapGetters }           from 'vuex';

import MainDialogHeader         from './TiktokCreateAdsMainDialogHeader.vue';
import MainDialogStepper        from './stepper/TiktokCreateAdsMainDialogStepper.vue';
import DialogAvailableSpintax   from './stepper/ads/TiktokCreateAdsMainDialogStepperAdsDialogAvailableSpintax.vue';
import DialogAdNameMacros       from './stepper/ads/TiktokCreateAdsMainDialogStepperAdsDialogNameMacros.vue';
import DialogAdsetNameMacros    from './stepper/adsets/TiktokCreateAdsMainDialogStepperAdsetsDialogNameMacros.vue';
import DialogAboutBudget        from './stepper/campaigns/TiktokCreateAdsMainDialogStepperCampaignsDialogAboutBudget.vue';
import DialogBudgetRandomize    from './stepper/campaigns/TiktokCreateAdsMainDialogStepperCampaignsDialogBudgetRandomize.vue';
import DialogCampaignNameMacros from './stepper/campaigns/TiktokCreateAdsMainDialogStepperCampaignsDialogNameMacros.vue';

export default {
  name: 'TiktokCreateAdsMainDialog',
  
  components: {
    MainDialogHeader,
    MainDialogStepper,
    DialogCampaignNameMacros,
    DialogAboutBudget,
    DialogBudgetRandomize,
    DialogAdsetNameMacros,
    DialogAdNameMacros,
    DialogAvailableSpintax
  },

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle',
      dialogs: 'tiktokCreateAds/dialogs'
    })
  },

  beforeMount() {
    const html = document.getElementsByTagName('html')[0];
    html.style.overflowY = 'hidden';
  },

  mounted() {
    if (this.bundle.id) {
      this.$refs.mainDialogHeader.setStepValidation();
    }
  },

  beforeDestroy() {
    const html = document.getElementsByTagName('html')[0];
    html.style.overflowY = 'scroll';
  },
  
  destroyed() {
    this.$store.dispatch('tiktokCreateAds/clearBundle');
  },

  methods: {
    handleMainButton(action) {
      this.$refs.mainDialogHeader.touch(action);
    }
  }
};
</script>