<template>
  <div
    v-if="showLink"
    class="pa-2"
  >
    <v-text-field
      v-if="adAccountSelected"
      :value="adAccountOptions[adAccount.id].link"
      :label="$t('createAds.wizard.adAccounts.link')"
      outlined
      dense
      :error-messages="linkErrorMessages"
      hide-details="auto"
      @input="setOptionValue(adAccount.id, 'link', $event)"
    />
    <v-skeleton-loader
      v-else
      boilerplate
      type="text"
      class="tiktok-upload-wizard-skeleton"
    />
  </div>
</template>

<script>
import validationMixin from '@/mixins/tiktok/common/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokWizardAdAccountsMainTableLink',

  mixins: [ validationMixin ],

  props: {
    adAccount: {
      type: Object,
      required: true
    }
  },

  computed: {
    ...mapGetters({
      selectedAdAccounts: 'tiktokCreateAdsWizard/selectedAdAccounts',
      adAccountOptions: 'tiktokCreateAdsWizard/adAccountOptions',
      selectedBundle: 'tiktokCreateAdsWizard/selectedBundle',
      $validation: 'tiktokCreateAdsWizard/$validation',
    }),

    adAccountSelected() {
      if (this.selectedAdAccounts.length === 0) return false;
      return this.selectedAdAccounts.findIndex(item => item.id === this.adAccount.id) > -1;
    },

    showLink() {
      return this.selectedBundle &&
        ['CONVERSIONS', 'TRAFFIC'].includes(this.selectedBundle.objective);
    },

    linkErrorMessages() {
      if (this.hasSpecificInvalid('adAccountOptions', this.adAccount.id, 'link')) {
        return this.$t('validation.protocol');
      }
      return null;
    }
  },

  methods: {
    setOptionValue(id, key, value) {
      this.$store.dispatch('tiktokCreateAdsWizard/setAdAccountOptionProperty', { id, key, value });
    },
  }
};
</script>