<template>
  <v-stepper-content
    :step="3"
    class="py-0"
  >
    <v-row>
      <v-col
        cols="12"
        md="6"
        offset-md="3"
        xl="4"
        offset-xl="4"
      >
        <v-card>
          <v-card-text>
            <!--   -->
            <ad-name />
            <!--    -->

            <!--  -->
            <v-text-field
              :value="bundle.profileName"
              :label="$t('upload.mainDialog.customIdentity')"
              outlined
              dense
              :error-messages="hasValidationInvalid('profileName') ? $t('common.required') : null"
              hide-details="auto"
              class="mt-3"
              @input="setValue('profileName', $event)"
            />
            <!--   -->

            <!--  -->
            <v-textarea
              :value="bundle.text"
              :label="$t('common.text')"
              rows="5"
              outlined 
              :error-messages="textErrors"
              hide-details="auto"
              class="mt-3"
              @input="setValue('text', $event)"
            />
            <span
              class="span-link"
              @click="$store.dispatch('tiktokCreateAds/openDialog', 'availableSpintax')"
            >{{ $t('common.availableSpintax') }}</span>
            <!--   -->

            <!--    -->
            <v-autocomplete
              :value="bundle.callToAction"
              :label="$t('upload.wizard.callToAction')"
              :items="ctaItems"
              dense
              outlined
              :error-messages="hasValidationInvalid('callToAction') ? $t('common.required') : null"
              hide-details="auto"
              class="mt-3"
              @change="setCta"
            />
            <!--     -->
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-stepper-content>
</template>

<script>
import { emojiRegex }  from '@/constants/regex';
import ctaLists        from '@/constants/tiktok/upload/cta';
import validationMixin from '@/mixins/validation_mixin';
import { mapGetters }  from 'vuex';

import AdName          from './TiktokCreateAdsMainDialogStepperAdsName.vue';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAds',

  components: {
    AdName
  },

  mixins: [ validationMixin ],

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle',
      profile: 'main/profile'
    }),

    ctaItems() {
      const lang = this.profile.language ? this.profile.language : 'en';
      return ctaLists[lang];
    },

    textHasEmoji() {
      return emojiRegex.test(this.bundle.text);
    },

    textErrors() {
      if (this.hasSpecificInvalid('text', 'required')) {
        return this.$t('common.required');
      }
      if (this.hasSpecificInvalid('text', 'noEmoji')) {
        return this.$t('validation.mustNotContain', {
          name: this.$t('common.emoji').toLowerCase()
        });
      }
      return null;
    }
  },

  methods: {
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },

    setCta(value) {
      this.setValue('callToAction', value);
      localStorage.setItem('dolphinTiktokBundleAdsCta', value);
    }
  }
};
</script>