<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.devices.devices') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <!--   -->
        <div class="d-flex align-center">
          <span>{{ $t('upload.devices.connectionType') }}</span>
          <v-btn-toggle
            :value="bundle.connectionType"
            borderless
            color="primary"
            mandatory
            multiple
            class="ml-2"
            @change="setConnectionType"
          >
            <v-btn
              v-for="(type, typeIndex) in connectionTypes"
              :key="`create-ads-main-dialog-devices-connection-type-${type}-${typeIndex}`"
              :value="type" 
              style="font-size: 12px; height: 24px;"
            >
              <span>{{ type === 'all' ? $t('upload.devices.all') : type }}</span>
            </v-btn>
          </v-btn-toggle>
        </div>
        <!--    -->

        <!--  -->
        <div class="mt-3 d-flex align-center">
          <span>{{ $t('upload.devices.operatingSystem.operatingSystem') }}</span>
          <v-btn-toggle
            :value="bundle.operatingSystem"
            borderless
            color="primary"
            mandatory
            class="ml-2"
            @change="setOperatingSystem"
          >
            <v-btn
              v-for="(i, index) in os"
              :key="`create-ads-main-dialog-devices-os-${i}-${index}`"
              :value="i" 
              style="font-size: 12px; height: 24px;"
            >
              <span>{{ $t(`upload.devices.operatingSystem.${i}`) }}</span>
            </v-btn>
          </v-btn-toggle>
        </div>
        <!--   -->

        <!--   -->
        <v-select
          :value="bundle.osVersion"
          :label="$t('upload.devices.osVersions')"
          :item-text="item => item.i18n_value" 
          :item-value="item => item.tiktokValue"
          :items="currentLocaleDevices"
          outlined
          dense
          hide-details
          return-object
          class="mt-3"
          @change="setOsVersion"
        />
        <!--    -->
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import osVersions     from '@/constants/tiktok/upload/os_versions';
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsDevices',

  data() {
    return {
      os: [
        'all',
        'android',
        'ios'
      ],
      connectionTypes: [
        'all',
        'wifi',
        '2g',
        '3g',
        '4g'
      ]
    };
  },

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle',
      profile: 'main/profile'
    }),

    currentLocaleDevices() {
      const lang = this.profile.language ? this.profile.language : 'en';
      let items = osVersions[lang].common;

      if (this.bundle.operatingSystem === 'android') {
        items = items.concat(osVersions[lang].android);
      } else if (this.bundle.operatingSystem === 'ios') {
        items = items.concat(osVersions[lang].ios);
      } else {
        items = items.concat(osVersions[lang].android, osVersions[lang].ios);
      }

      return items.map(item => {
        if (item.os) {
          item.tiktokValue = `${item.os}:${item.id}`;
        } else if (item.value === 'all') {
          item.tiktokValue = 'undefined:1300';
        } else {
          item.tiktokValue = item.value;
        }
        return item;
      });
    }
  },

  methods: {
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },

    setOperatingSystem(value) {
      this.setValue('osVersion', 'undefined:1300');
      this.setValue('operatingSystem', value);
    },

    setOsVersion({ os, tiktokValue }) {
      if (os === 'android' && this.bundle.operatingSystem !== 'android') {
        this.setValue('operatingSystem', 'android');
      }
      if (os === 'ios' && this.bundle.operatingSystem !== 'ios') {
        this.setValue('operatingSystem', 'ios');
      }
      this.setValue('osVersion', tiktokValue);
    },

    setConnectionType(value) {
      const lastItem = Array.isArray(value) && value.length ? value[value.length - 1] : null;

      if (lastItem && lastItem === 'all') {
        this.setValue('connectionType', ['all']);
      } else if (value.length === 4) {
        this.setValue('connectionType', ['all']);
      } else {
        this.setValue('connectionType', value.filter(i => i !== 'all'));
      }
    },
  }
};
</script>