<template>
  <v-stepper
    v-model="currentStep"
    class="create-ads-wizard"
  >
    <v-stepper-header class="create-ads-wizard-header">
      <v-stepper-step
        v-for="(step, stepIndex) in steps"
        :key="`tiktok-wizard-stepper-${step}-${stepIndex}`"
        :step="stepIndex + 1"
        :complete="currentStep > (stepIndex + 1)"
        :editable="isEditable(step)"
        class="py-0"
      >
        <v-badge
          :value="step === 'adAccounts'"
          color="red"
          inline
          dark
          tile
          style="text-shadow: none;"
          :content="selectedAdAccounts && selectedAdAccounts.length !== 0 ? selectedAdAccounts.length : '0'"
        >
          {{ $t(`upload.wizard.${step}`) }}
        </v-badge>
      </v-stepper-step>
    </v-stepper-header>

    <v-stepper-items class="create-ads-wizard-items">
      <!--  -->
      <settings />

      <!--  -->
      <ad-accounts />
      
      <!--  -->
      <creatives />
    </v-stepper-items>

    <!--   -->
    <v-btn
      v-if="currentStep !== 3"
      class="tiktok-wizard-main-button"
      color="primary"
      style="z-index: 201;"
      :disabled="disabled"
      @click="touch('continue')"
    >
      {{ $t('common.continue') }}
    </v-btn>
    <!--    -->

    <!--    -->
    <v-btn
      v-else
      :loading="loading"
      class="tiktok-wizard-main-button"
      color="primary"
      style="z-index: 201;"
      @click="touch('create')"
    >
      {{ $t('common.create') }}
    </v-btn>
    <!--     -->

    <!--   -->
    <template>
      <div class="text-center">
        <v-dialog
          v-model="dialog"
          width="500"
        >
          <v-card>
            <v-card-text />

            <v-card-text>
              {{ $t(`upload.wizard.notification`) }}
            </v-card-text>

            <v-divider />

            <v-card-actions>
              <v-spacer />
              <v-btn
                color="primary"
                text
                @click="dialog = false"
              >
                Ok
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </div>
    </template>
    <!--   -->
  </v-stepper>
</template>

<script>
import { required }   from 'vuelidate/lib/validators';
import { mapFields }  from 'vuex-map-fields';
import { mapGetters } from 'vuex';

import AdAccounts     from './adAccounts/TiktokWizardAdAccounts.vue';
import Creatives      from './creatives/TiktokWizardCreatives.vue';
import Settings       from './settings/TiktokWizardSettings.vue';

const hasProtocol = (value) => {
  return value.toString().toLowerCase().startsWith('http://') ||
    value.toString().toLowerCase().startsWith('https://');
};

const invalidText = (value) => {
  return value.length <= 100;
};

export default {
  name: 'TiktokWizardMainStepper',

  components: {
    Settings,
    AdAccounts,
    Creatives
  },

  data() {
    return {
      loading: false,
      steps: ['settings', 'adAccounts', 'creatives'],
      currentStep: 1,
      disabled: false,
      dialog: false,
      showedCount: 1
    };
  },

  validations () {
    if (this.currentStep === 1) {
      const selectedBundle = { required };
      return { selectedBundle };
    }
    if (this.currentStep === 2) {
      const selectedAdAccounts = { required };

      const adAccountOptions = {};
      if (this.selectedAdAccounts.length) {
        for (const key in this.adAccountOptions) {
          //     
          if (!this.isSelected(key)) continue;
        
          adAccountOptions[key] = {
            budget: { required },
          };

          if (this.selectedBundle.objective === 'CONVERSIONS') {
            adAccountOptions[key].pixel = { required };
            adAccountOptions[key].pixelEvent = { required };
            adAccountOptions[key].link = { required, hasProtocol };
          }
          if (this.selectedBundle.objective === 'TRAFFIC') {
            adAccountOptions[key].link = { required, hasProtocol };
          }
          if (this.selectedBundle.objective === 'APP_INSTALLS') {
            adAccountOptions[key].app = { required };
            if (this.selectedBundle.optimizationGoal === 'in_app_event') {
              adAccountOptions[key].appEvent = { required };
            }
          }
        }
      } else {
        return { selectedAdAccounts };
      }
      return { selectedAdAccounts, adAccountOptions };
    }
    if (this.currentStep === 3) {
      const adAccountOptions = {};
      if (this.selectedAdAccounts.length) {
        for (const key in this.adAccountOptions) {
          //     
          if (!this.isSelected(key)) continue;
        
          adAccountOptions[key] = {
            text: { required, invalidText },
            callToAction: { required },
            creatives: { required },
          };

          if (this.selectedBundle.objective === 'CONVERSIONS') {
            adAccountOptions[key].pixel = { required };
            adAccountOptions[key].pixelEvent = { required };
          }
          if (this.selectedBundle.objective === 'APP_INSTALLS') {
            adAccountOptions[key].app = { required };
          }
        }
      }
      return { adAccountOptions };
    }

    return {};
  },

  computed: {
    ...mapGetters({
      selectedBundle: 'tiktokCreateAdsWizard/selectedBundle',
      uploadBundle: 'tiktokCreateAdsWizard/uploadBundle',
      $validation: 'tiktokCreateAdsWizard/$validation',
      adAccounts: 'tiktokAdsmanagerAdAccounts/selected',
    }),
    ...mapFields('tiktokCreateAdsWizard', [
      'selectedAdAccounts',
      'adAccountOptions'
    ]),
  },

  watch: {
    currentStep() {
      if (this.$validation) {
        this.$store.dispatch('tiktokCreateAdsWizard/setValidation', null);
      }

      //     
      this.setStepValidation();
    },

    adAccountOptions: {
      deep: true,
      handler() {
        if (this.$validation) {
          this.$store.dispatch('tiktokCreateAdsWizard/setValidation', null);
        }
        //     
        this.setStepValidation();
      }
    },
    uploadBundle: {
      deep: true,
      handler() {
        if (this.$validation) {
          this.$store.dispatch('tiktokCreateAdsWizard/setValidation', null);
        }
        //     
        this.setStepValidation();
      }
    },
  },

  methods: {
    setStepValidation() {
      if (!this.uploadBundle) return;
      //     
      const isValid = !this.$v.$invalid;
      switch (this.currentStep) {
      case 1:
        this.setUploadBundleValue('settingsStepValid', isValid);
        break;
      case 2:
        this.setUploadBundleValue('adAccountsStepValid', isValid);
        break;
      case 3:
        this.setUploadBundleValue('creativesStepValid', isValid);
        break;
      }
    },

    setUploadBundleValue(key, value) {
      this.$store.dispatch('tiktokCreateAdsWizard/setUploadBundleProperty', { key, value });
    },

    isEditable(step) {
      if (step === 'settings') {
        return true;
      } else if (step === 'adAccounts' && this.uploadBundle && this.uploadBundle.settingsStepValid) {
        return true;
      } else if (step === 'creatives' && this.uploadBundle && this.uploadBundle.adAccountsStepValid) {
        return true;
      } else {
        return false;
      }
    },

    showDialog() {
      if(this.showedCount === 1) {
        this.dialog = true;
      }

      this.showedCount++;
    },
    
    touch(action) {
      this.$v.$touch();
      
      if (this.$v.$invalid) {
        this.$store.dispatch('tiktokCreateAdsWizard/setValidation', this.$v);
      } else {
        switch (action) {
        case 'continue':
          this.currentStep++;
          break;
        case 'create':
          this.create();
          break;
        }
      }
    },

    async create() {
      const jobs = this.getJobOptions();

      for (const job of jobs) {
        for (const key of Object.keys(job)) {
          if (job[key] !== null && job[key].toString().length === 0) {
            job[key] = null;
          }
        }
      }

      try {
        this.loading = true;

        const response = await this.api.post('/tiktok/create-ads/jobs', { jobs });
        if (response.status === 200) {
          this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });
          
          this.loading = false;
          this.$router.push({ path: '/tiktok/create-ads/jobs', params: { tab: 'jobs' } });
          this.selectedAdAccounts = [];
          this.adAccountOptions = {};
          await this.$store.dispatch('tiktokAdsmanagerAdAccounts/clearSelected');
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    },

    getJobOptions() {
      const jobOptions = [];

      for (const key of Object.keys(this.adAccountOptions)) {
        //     
        if (!this.isSelected(key)) continue;
        
        const {
          app,
          pixel,
          pixelEvent,
          link,
          budget,
          text,
          callToAction,
          creatives,
          bid } = this.adAccountOptions[key];
          
        jobOptions.push({
          bundle_id: this.selectedBundle.id,
          campaigns_count: this.uploadBundle.campaignsQuantity,
          adsets_count: this.uploadBundle.adsetsQuantity,
          adaccount_id: key,
          app_id: app ? app.id : null,
          pixel_id: pixel ? pixel.id : null,
          pixel_event_id: pixelEvent ? pixelEvent.id : null,
          link: link,
          budget: budget,
          call_to_action: callToAction,
          text: text,
          creatives: creatives.map(item => item.filename),
          creative_to_adset_mode: this.uploadBundle.creativeToAdsetMode,
          bid: bid
        });
      }

      return jobOptions;
    },

    isSelected(adAccountId) {
      return this.selectedAdAccounts.findIndex(i => i.id === adAccountId) > -1;
    }
  }
};
</script>
