import { mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters({
      $validation: 'tiktokCreateAds/$validation'
    })
  },
  methods: {
    hasValidationInvalid(property) {
      if (this.$validation) {
        const { bundle } = this.$validation;
        if (bundle && bundle[property]) {
          return bundle[property].$invalid;
        }
      }
      return false;
    },
    hasSpecificInvalid(property, subproperty) {
      if (this.$validation) {
        const { bundle } = this.$validation;
        if (bundle && bundle[property]) {
          return !bundle[property][subproperty];
        }
      }
      return false;
    },
  }
};