<template>
  <v-card>
    <v-card-text style="padding: 0px 8px 0px 8px">
      <!--  -->
      <v-data-table
        v-model="selectedJobs"
        :headers="cols"
        fixed-header
        hide-default-footer
        :height="innerHeight - 50 - 48 - 48"
        show-select
        :items="mainTableData"
        :items-per-page="footerOptions.itemsPerPage"
        :loading="mainTableLoading"
      >
        <!-- ID -->
        <template #[`item.id`]="{ item }">
          <span style="font-family: monospace;">
            {{ item.id }}
          </span>
          <v-btn
            color="grey"
            icon
            small
            @click="copyToClipboard(item.id)"
          >
            <v-icon :size="14">
              fas fa-copy
            </v-icon>
          </v-btn>
        </template>
        <!-- ID -->

        <!--  -->
        <template #[`item.name`]="{ item }">
          <template v-if="item.bundle">
            {{ item.bundle.name }}
          </template>
          <template v-if="item.adaccount">
            &rarr;
            {{ item.adaccount.name }}
          </template>
        </template>
        <!--   -->

        <!--  -->
        <template #[`item.time`]="{ item }">
          {{ moment(item.created_at).calendar() }}
        </template>
        <!--   -->

        <!--  -->
        <template #[`item.logs`]="{ item }">
          <v-alert
            v-if="item.log && item.has_error === 1"
            class="vue-alert"
            color="red"
            dense
            text
            icon="error"
            type="error"
          >
            {{ item.log }}
          </v-alert>
        </template>
        <!--   -->

        <!--  -->
        <template #[`item.status`]="{ item }">
          <main-table-status :job="item" />
        </template>
        <!--   -->

        <!--  -->
        <template #footer>
          <v-data-footer
            show-first-last-page
            :options="footerOptions"
            :pagination="footerPagination"
            :items-per-page-options="[10, 30, 50, 100]"
            @update:options="updateFooterOptions"
          />
        </template>
        <!--   -->
      </v-data-table>
    </v-card-text>
  </v-card>
</template>

<script>
import clipboard       from '@/mixins/clipboard';
import footerDataMixin from '@/mixins/tiktok/common/footer_data';
import { mapFields }   from 'vuex-map-fields';
import { mapGetters }  from 'vuex';

import MainTableStatus from './mainTable/TiktokJobsMainTableStatus.vue';

export default {
  name: 'TiktokJobsMainTable',

  components: {
    MainTableStatus
  },

  mixins: [ footerDataMixin, clipboard ],

  data() {
    return {
      updateInterval: null,
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
      innerHeight: 'main/innerHeight',
      mainTableData: 'tiktokCreateAdsWizardJobs/mainTableData',
      mainTableLoading: 'tiktokCreateAdsWizardJobs/mainTableLoading',
      mainTablePagination: 'tiktokCreateAdsWizardJobs/mainTablePagination',
    }),
    ...mapFields('tiktokCreateAdsWizardJobs', ['selectedJobs']),

    cols() {
      return [
        { text: 'ID', value: 'id', width: 380 },
        { text: this.$t('common.name'), value: 'name', width: 300 },
        { text: this.$t('autoRules.fbRules.static.parameter.created_time'), value: 'time', width: 300 },
        { value: 'logs', width: 300 , sortable: false },
        { text: this.$t('common.status'), value: 'status' }
      ];
    },
  },

  destroyed() {
    clearInterval(this.updateInterval);
  },

  methods: {
    async updateFooterOptions({ page, itemsPerPage }) {
      await this.$store.dispatch('tiktokCreateAdsWizardJobs/loadMainTableData', {
        endpoint: 'tiktok/create-ads/jobs',
        page,
        itemsPerPage
      });
      this.setStatusesInterval();
    },

    setJobNewData(data) {
      this.$store.dispatch('tiktokCreateAdsWizardJobs/editMainTableJob', data);
    },

    setStatusesInterval() {
      const ids = this.mainTableData.filter(i => this.badStatus(i)).map(i => i.id);

      if (Array.isArray(ids) && ids.length) {
        clearInterval(this.updateInterval);

        this.getJobsStatuses();
        this.updateInterval = setInterval(() => {
          this.getJobsStatuses();
        }, 5000);
      }
    },

    async getJobsStatuses() {
      try {
        const ids = this.mainTableData.filter(i => this.badStatus(i)).map(i => i.id);

        const response =  await this.api.post('/tiktok/create-ads/jobs/statuses', { ids });
        const { status, data } = response;

        if (status === 200 && data && data.data && Array.isArray(data.data.statuses)) {
          const uncompletedJobs = data.data.statuses.filter(status => this.badStatus(status));
          if (uncompletedJobs.length === 0) {
            clearInterval(this.updateInterval);
          }

          for (const status of data.data.statuses) {
            this.setJobNewData(status);
          }
        }
      } catch (error) {
        console.log(error);
      }
    },

    badStatus(job) {
      return parseInt(job.has_error, 10) === 0 &&
        parseInt(job.success, 10) === 0 &&
        parseInt(job.stage, 10) < 4;
    }
  }
};
</script>
