<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.bundles.mainDialog.cardsTitles.bidStrategies') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <!--   -->
        <v-select
          :label="$t('upload.bundles.mainDialog.cardsTitles.bidStrategies')"
          :value="bundle.adsetBidStrategy"
          :item-text="item => $t(`upload.bid_strategies.${item.text}`)" 
          :item-value="item => item.value"
          :items="bidStrategies"
          outlined
          dense
          hide-details
          @change="setValue('adsetBidStrategy', $event)"
        />
        <!--    -->

        <!--  -->
        <div
          v-if="bundle.adsetBidStrategy === 'MANUAL'" 
          class="d-flex align-center mt-3"
        >
          <span>{{ $t('common.bidAmount') }}</span>
          <v-text-field
            :value="bundle.bid"
            suffix="USD"
            type="number"
            step="0.01"
            min="0.01"
            hide-details="auto"
            outlined
            dense
            :error-messages="budgetTypeErrors"
            class="ml-2"
            style="max-width: 125px;"
            @input="setValue('bid', $event)"
            @blur="blurBudget"
          />

          <!-- <v-btn
            icon
            small
            color="primary"
            class="ml-1"
            @click="$store.dispatch('tiktokCreateAds/openDialog', 'aboutBudget')"
          >
            <v-icon :size="16">
              far fa-question-circle
            </v-icon>
          </v-btn> -->
        </div>
        <!--   -->

        <!--   -->
        <div class="mt-3 d-flex align-center">
          <span>{{ $t('createAds.mainDialog.deliveryType.deliveryType') }}</span>
          <v-btn-toggle
            :value="bundle.deliveryType"
            borderless
            color="primary"
            mandatory
            class="ml-2"
            @change="setDeliveryType"
          >
            <v-btn
              v-for="(type, typeIndex) in deliveryTypes"
              :key="`create-ads-main-dialog-bid-delivery-type-${type}-${typeIndex}`"
              :value="type" 
              style="font-size: 12px; height: 24px;"
              :disabled="bundle.adsetBidStrategy !== 'MANUAL'"
            >
              <span>{{ $t(`createAds.mainDialog.deliveryType.${type}`) }}</span>
            </v-btn>
          </v-btn-toggle>
        </div>
        <!--    -->
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import validationMixin from '@/mixins/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsBidStrategy',

  mixins: [ validationMixin ],

  data() {
    return {
      bidStrategies: [
        { text: 'lowest_cost', value: 'LOWEST_COST' },
        { text: 'manual', value: 'MANUAL' },
      ],
      deliveryTypes: ['standard', 'accelerated']
    };
  },

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle'
    }),

    budgetTypeErrors() {
      if (this.hasValidationInvalid('bid')) {
        return this.$t('common.required');
      }
      return null;
    }
  },

  watch: {
    'bundle.adsetBidStrategy'(newValue) {
      if (newValue !== 'MANUAL') {
        this.setValue('deliveryType', 'standard');
      } else {
        const deliveryType = localStorage.getItem('dolphinTiktokBundleAdsetsDeliveryType');
        if (deliveryType) {
          this.setValue('deliveryType', deliveryType);
        }
      }
    }
  },

  methods: {
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },

    blurBudget() {
      if (this.bundle.bid < 0) {
        this.setValue('bid', '0');
      }
    },

    setDeliveryType(value) {
      this.setValue('deliveryType', value);
      localStorage.setItem('dolphinTiktokBundleAdsetsDeliveryType', value);
    }
  }
};
</script>
