<template>
  <v-dialog
    :value="dialogs.creativesForAll"
    width="500"
    :transition="false"
    scrollable
    @click:outside="closeDialog"
    @keydown.esc="closeDialog"
  >
    <v-card>
      <v-toolbar
        :height="48"
        dark
        color="primary"
        class="mb-5"
        flat
      >
        <v-toolbar-title>
          {{ $t('upload.wizard.creativesForAll') }}
        </v-toolbar-title>

        <v-spacer />

        <v-toolbar-items>
          <v-btn
            icon
            @click="closeDialog"
          >
            <v-icon>
              fas fa-times
            </v-icon>
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-card-text>
        <v-carousel
          v-if="hasCreatives"
          height="150px"
          next-icon="keyboard_arrow_right"
          prev-icon="keyboard_arrow_left"
          class="tiktok-wizard-creatives-carousel"
        >
          <v-carousel-item
            v-for="creative in creatives"
            :key="creative.filename"
          > 
            <v-row
              class="flex-nowrap"
              style="height: 100%"
            > 
              <v-col class="text-center pa-0"> 
                <div class="wizard-creatives-main-table-video-wrapper">
                  <video
                    :src="creative.url.replace('http://127.0.0.1:3056', 'https://cloud.dolphin.ru.com')" 
                    muted
                    loop
                    class="wizard-creatives-main-table-video"
                    @mouseover="hoverCreative = creative.filename"
                  />
                  <div
                    v-show="showOverlay(creative.filename)"
                    class="wizard-creatives-main-table-video-overlay"
                    @mouseleave="hoverCreative = null"
                  >
                    <v-chip
                      color="red"
                      text
                      small
                      @click="removeCreative(creative.filename)"
                    >
                      {{ $t('common.delete') }}
                    </v-chip>
                  </div>
                </div>
              </v-col>  
            </v-row> 
          </v-carousel-item>
        </v-carousel>
    
        <!--    -->
        <v-col class="text-center">
          <v-btn
            text
            color="primary"
            small
            :loading="sendingCreative"
            @click="openFileUploader"
          >
            <v-icon
              :size="18"
              class="mr-1"
            >
              add
            </v-icon>
            {{ $t('upload.wizard.static.addCreatives') }}
          </v-btn>
          <!--     -->

          <!--    -->
          <input
            v-show="false"
            ref="inputUpload"
            type="file"
            multiple
            @change="handleInputUpload"
          >

          <!--     -->
        </v-col>
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <v-btn
          text
          color="grey"
          @click="closeDialog"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          text
          color="primary"
          :loading="sendingCreative"
          @click="add"
        >
          {{ $t('common.add') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokWizardCreativesDialogsCreativesForAll',

  data() {
    return {
      sendingCreative: false,
      hoverCreative: null,
      creatives: [],
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokCreateAdsWizard/dialogs',
      uploadBundle: 'tiktokCreateAdsWizard/uploadBundle',
      selectedAdAccounts: 'tiktokCreateAdsWizard/selectedAdAccounts',
    }),

    hasCreatives() {
      return !!this.creatives.length;
    },
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokCreateAdsWizard/closeDialog', 'creativesForAll');
    },

    openFileUploader () {
      this.$refs.inputUpload.value = '';
      this.$refs.inputUpload.click();
    },

    async sendCreativeToServer(file) {
      try {
        let formData = new FormData();
        formData.append('file', file);
        const options = {
          headers: { 'Content-Type': 'multipart/form-data' }
        };
        const response = await this.api.post('tiktok/create-ads/upload-media', formData, options);
        const { status, data } = response;

        if (status === 200 && data && data.data) {
          return data.data;
        } else {
          throw new Error('invalid response');
        }
      } catch (error) {
        console.log(error);
        return null;
      }
    },

    setCreativeValue(creative) {
      this.creatives.push(creative);
    },

    removeCreative(creativeName) {
      this.creatives = this.creatives.filter(i => i.filename !== creativeName);
    },

    showOverlay(creativeName) {
      if (this.hoverCreative === creativeName) {
        return true;
      }
      return false;
    },

    async handleInputUpload () {
      try {
        this.sendingCreative = true;

        for (const file of this.$refs.inputUpload.files) {
          const { filename, url} = await this.sendCreativeToServer(file);

          if (filename && url) {
            const creative = {
              original_filename: file.name,
              url: url,
              type: file.type,
              size: file.size,
              filename: filename
            };
            this.setCreativeValue(creative);
          }
        }
      } catch (error) {
        console.log(error);
      } finally {
        this.sendingCreative = false;
      }
    },

    add() {
      for (const selectedAdAccount of this.selectedAdAccounts) {
        for (const creative of this.creatives) {
          this.$store.dispatch('tiktokCreateAdsWizard/addAdAccountOptionCreo', {
            id: selectedAdAccount.id,
            value: creative
          });
        }
      }

      this.closeDialog();
    }
  }
};
</script>

