<template>
  <div class="d-flex align-center justify-space-between mb-2">
    <div>
      <!--  -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            icon
            @click="bulkDelete"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash-alt
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>
    </div>

    <div class="d-flex align-center">
      <!--   -   -->
      <v-btn
        small
        color="primary"
        text
        style="min-width: 0; margin-top: 1px;"
        class="ml-3"
        @click="createBundle"
      >
        <v-icon :size="18">
          add
        </v-icon>

        {{ $t('upload.bundles.topbar.createBundle') }}
      </v-btn>

      <div class="d-flex align-center ">
        <!--    -->
        <v-text-field
          dense
          clearable
          outlined
          :label="$t('filters.searchByNameOrID')"
          single-line
          prepend-inner-icon="fas fa-search"
          hide-details
          :value="searchText"
          @input="filterName"
        />
        <!--     -->
      </div>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex';

export default {
  name: 'TiktokBundlesTopbar',

  data() {
    return {
      searchText: '',
    };
  },

  computed: {
    ...mapGetters({
      selected: 'tiktokCreateAds/selected',
    }),
  },

  methods: {
    filterName(search) {
      this.searchText = search;
      setTimeout(async () => {
        if (search === this.searchText) {
          await this.$store.dispatch('tiktokCreateAds/loadMainTableData', {
            endpoint: 'tiktok/bundles',
            query: search
          });
        }
      }, 500);
    },
    async createBundle() {
      await this.$store.dispatch('tiktokCreateAds/setBundle');
      this.$store.dispatch('tiktokCreateAds/openDialog', 'mainDialog');
    },

    async bulkDelete() {
      this.$store.dispatch('tiktokCreateAds/openDialog', 'bulkDelete');
    }
  }
};
</script>
