<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('common.budget') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <!--   -->
        <span
          v-if="bundle.objective !== 'REACH'"
          class="mr-2"
        >{{ $t('upload.bundles.mainDialog.budgetLevel') }}</span>
        <v-btn-toggle
          v-if="bundle.objective !== 'REACH'"
          :value="bundle.budgetLevel"
          borderless
          color="primary"
          mandatory
          @change="setBudgetLevel"
        >
          <v-btn 
            value="campaign" 
            style="font-size: 12px; height: 24px;"
          >
            <span>{{ $t('common.campaign') }}</span>
          </v-btn>

          <v-btn 
            value="adset" 
            style="font-size: 12px; height: 24px;"
          >
            <span>{{ $t('common.adset') }}</span>
          </v-btn>
        </v-btn-toggle>
        <!--    -->

        <!--  -->
        <div class="d-flex align-center mt-3">
          <span>{{ $t('common.budget') }}:</span>
          <v-text-field
            :value="bundle.budget"
            suffix="USD"
            type="number"
            min="0"
            outlined
            dense
            :error-messages="hasValidationInvalid('budget') ? $t('validation.min', { length: minBudgetSum }) : null"
            hide-details="auto"
            class="ml-2"
            style="max-width: 125px;"
            @input="setBudget"
            @blur="blurBudget"
          />

          <v-btn
            icon
            small
            color="primary"
            class="ml-1"
            @click="$store.dispatch('tiktokCreateAds/openDialog', 'aboutBudget')"
          >
            <v-icon :size="16">
              far fa-question-circle
            </v-icon>
          </v-btn>

          <!--  -->
          <v-switch
            :input-value="bundle.budgetRandomize"
            :label="$t('common.randomize')"
            dense
            hide-details
            class="mt-0 pt-0 ml-3"
            @change="setBudgetRandomize"
          />

          <v-text-field
            v-if="bundle.budgetRandomize"
            :value="bundle.budgetRandomValue"
            prefix=""
            suffix="%"
            type="number"
            min="1"
            max="99"
            outlined
            dense
            hide-details
            class="create-ads-main-dialog-campaigns-budget-randomize ml-2"
            @input="setBudgetRandomValue"
            @blur="blurBudgetRandomValue"
          />
          <v-btn
            v-if="bundle.budgetRandomize"
            icon
            small
            color="primary"
            class="ml-1"
            @click="$store.dispatch('tiktokCreateAds/openDialog', 'budgetRandomize')"
          >
            <v-icon :size="16">
              far fa-question-circle
            </v-icon>
          </v-btn>
        </div>
        <!--   -->
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import validationMixin from '@/mixins/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperCampaignsBudget',

  mixins: [ validationMixin ],

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle'
    }),

    minBudgetSum() {
      if (this.bundle.budgetLevel === 'campaign') {
        return 50;
      }
      return 20;
    }
  },

  methods: {
    blurBudget() {
      if (this.bundle.budget < 0) {
        this.setBudget('0');
      }
    },
    blurBudgetRandomValue() {
      if (this.bundle.budgetRandomValue < 1) {
        this.setBudgetRandomValue('1');
      } else if (this.bundle.budgetRandomValue > 99) {
        this.setBudgetRandomValue('99');
      }
    },

    setBudgetLevel(budgetLevel) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'budgetLevel',
        value: budgetLevel
      });
    },

    setBudget(budget) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'budget',
        value: budget
      });
    },

    setBudgetRandomize(value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'budgetRandomize',
        value: value
      });
    },

    setBudgetRandomValue(budgetRandomValue) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'budgetRandomValue',
        value: budgetRandomValue
      });
    },
  }
};
</script>