<template>
  <v-toolbar
    class="create-ads-main-dialog-header"
    color="primary"
    flat
    dark
    dense
  >
    <!--   -->
    <v-btn
      icon
      small
      @click="$store.dispatch('tiktokCreateAds/closeDialog', 'mainDialog')"
    >
      <v-icon :size="18">
        fas fa-times
      </v-icon>
    </v-btn>
    <!--    -->

    <!--  -->
    <v-toolbar-title>{{ dialogTitle }}</v-toolbar-title>
    <!--   -->
        
    <v-spacer />

    <!--   -->
    <v-toolbar-items>
      <!--  -->
      <v-btn
        text
        @click="$store.dispatch('tiktokCreateAds/closeDialog', 'mainDialog')"
      >
        {{ $t('common.cancel') }}
      </v-btn>
      <!--   -->

      <!--  -->
      <v-btn
        v-if="bundle.id"
        text
        :disabled="bundle.step !== 3"
        :loading="loading"
        @click="touch('edit')"
      >
        {{ $t('common.edit') }}
      </v-btn>
      <v-btn
        v-else
        text
        :disabled="bundle.step !== 3"
        :loading="loading"
        @click="touch('create')"
      >
        {{ $t('common.create') }}
      </v-btn>
      <!--   -->
    </v-toolbar-items>
    <!--    -->
  </v-toolbar>
</template>
<script>
import { emojiRegex }         from '@/constants/regex';
import { required, minValue } from 'vuelidate/lib/validators';
import { mapGetters }         from 'vuex';

const noEmoji = (value) => !emojiRegex.test(value);

export default {
  name: 'TiktokCreateAdsMainDialogHeader',

  data() {
    return {
      loading: false,
      bundleName: '',
      path: '/tiktok/create-ads/bundles'
    };
  },
  
  validations () {
    const bundle = {};

    if (this.bundle.step === 1) {
      bundle.name = { required };
      bundle.campaignName = { required };
      if (this.bundle.budgetLevel === 'campaign') {
        bundle.budget = { required, minValue: minValue(50) };
      }
      if (this.bundle.budgetLevel === 'adset') {
        bundle.budget = { required, minValue: minValue(20) };
      }
      // if (this.bundle.campaignBidStrategy === 'MANUAL') {
      //   bundle.campaignBudget = { required, minValue: minValue(50) };
      // }
    }

    if (this.bundle.step === 2) {
      bundle.adsetName = { required };
      bundle.location = { required };
      if (this.bundle.adsetBidStrategy === 'MANUAL') {
        bundle.bid = { required, minValue: minValue(0.01) };
      }
    }

    if (this.bundle.step === 3) {
      bundle.adName = { required };
      bundle.profileName = { required };
      bundle.text = {
        required,
        noEmoji
      };
      bundle.callToAction = { required };
    }

    return { bundle };
  },

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle',
      $validation: 'tiktokCreateAds/$validation'
    }),

    dialogTitle() {
      if (this.bundle.id) {
        return `${this.$t('common.edit')} ${this.bundleName}`;
      }
      return this.$t('upload.bundles.topbar.createBundle');
    }
  },

  watch: {
    bundle: {
      deep: true,
      handler() {
        //      
        if (this.$validation) {
          this.$store.dispatch('tiktokCreateAds/setValidation', null);
        }

        //     
        this.setStepValidation();
      }
    },
  },

  created() {
    this.bundleName = this.bundle.name;
    if (this.bundle.id) {
      this.setValue('campaignsStepValid', true);
      this.setValue('adsetsStepValid', true);
      this.setValue('adsStepValid', true);
    }
  },

  methods: {
    setStepValidation() {
      const isValid = !this.$v.$invalid;
      switch (this.bundle.step) {
      case 1:
        this.setValue('campaignsStepValid', isValid);
        break;
      case 2:
        this.setValue('adsetsStepValid', isValid);
        break;
      case 3:
        this.setValue('adsStepValid', isValid);
        break;
      }
    },
    
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },
    
    touch(action) {
      this.$v.$touch();

      if (this.$v.$invalid) {
        this.$store.dispatch('tiktokCreateAds/setValidation', this.$v);
      } else {
        switch (action) {
        case 'continue':
          this.setValue('step', this.bundle.step + 1);
          break;
        case 'create':
          this.create();
          break;
        case 'edit':
          this.edit();
          break;
        }
      }
    },

    async create() {
      try {
        this.loading = true;

        const data = this.createData();
        const response = await this.api.post('/tiktok/bundles', data);
        if (response.status === 201) {
          this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });

          this.$store.dispatch('tiktokCreateAds/loadMainTableData', { endpoint: 'tiktok/bundles' });

          this.$store.dispatch('tiktokCreateAds/closeDialog', 'mainDialog');

          if (this.$router.currentRoute.path !== this.path) {
            this.$router.push(this.path);
          }

        } else {
          throw new Error('status not 201');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    },
    
    async edit() {
      try {
        this.loading = true;

        const data = this.createData();
        const response = await this.api.patch(`/tiktok/bundles/${this.bundle.id}`, data);
        if (response.status === 200) {
          this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });

          this.$store.dispatch('tiktokCreateAds/loadMainTableData', { endpoint: 'tiktok/bundles' });

          this.$store.dispatch('tiktokCreateAds/closeDialog', 'mainDialog');
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    },

    createData() {
      const data = {
        name: this.bundle.name,
        objective: this.bundle.objective,
        campaignName: this.bundle.campaignName,
        budgetLevel: this.bundle.budgetLevel,
        budget: this.bundle.budget,
        budgetRandomize: this.bundle.budgetRandomize,
        budgetRandomValue: this.bundle.budgetRandomValue,
        adsetBidStrategy: this.bundle.adsetBidStrategy,
        // campaignBidStrategy: this.bundle.campaignBidStrategy,
        bid: this.bundle.bid ? this.bundle.bid : 0,
        // campaignBudget: this.bundle.campaignBudget ? this.bundle.campaignBudget : 0,
        adsetName: this.bundle.adsetName,
        age: this.bundle.age,
        gender: this.bundle.gender,
        placement: this.bundle.placement,
        placementsManual: this.bundle.placementsManual,
        placementUserComment: this.bundle.placementUserComment,
        placementVideoDownload: this.bundle.placementVideoDownload,
        connectionType: this.bundle.connectionType,
        operatingSystem: this.bundle.operatingSystem,
        osVersion: this.bundle.osVersion,
        locale: this.bundle.locale,
        adName: this.bundle.adName,
        profileName: this.bundle.profileName,
        text: this.bundle.text,
        callToAction: this.bundle.callToAction,
        delivery_type: this.bundle.deliveryType,
        location: this.bundle.location
      };

      if (this.bundle.objective === 'APP_INSTALLS') {
        data.optimizationGoal = this.bundle.optimizationGoal;
      }

      // let locations = [];
      // for (const location of this.bundle.location) {
      //   locations = locations.concat(this.findParentIds(areasEn, location.id));
      // }
      // data.location = [...new Set(locations)];

      return data;
    },

    findParentIds(areas, id) {
      let foundAreas = [];

      for (const area of areas) {
        if (area.id === id) {
          foundAreas.push(area.id);
        } else if (Array.isArray(area.children)) {
          const foundInChildren = this.findParentIds(area.children, id);

          if (Array.isArray(foundInChildren) && foundInChildren.length) {
            foundAreas = foundAreas.concat([area.id], foundInChildren);
          }
        }
      }
      
      return foundAreas;
    }
  }
};
</script>
