<template>
  <v-tabs
    centered
    grow
    :value="tabNumber"
    :height="32"
  >
    <!--  -->
    <v-tab
      key="tab-bundles"
      :value="0"
      @click="selectTab('bundles')"
    >
      {{ $t('upload.tabs.bundles') }}
    </v-tab>

    <!--   -->
    <v-tab 
      key="tab-wizard"
      :value="1"
      @click="selectTab('wizard')"
    >
      {{ $t('upload.tabs.wizard') }}
    </v-tab>

    <!--   -->
    <v-tab
      key="tab-jobs"
      @click="selectTab('jobs')"
    >
      {{ $t('upload.tabs.jobs') }}
    </v-tab>
  </v-tabs>
</template>

<script>
export default {
  name: 'TiktokCreateAdsTabs',

  data: () => ({
    tabs: ['bundles', 'wizard', 'jobs',],
  }),

  computed: {
    tabNumber() {
      switch (this.$route.params.tab) {
      case 'bundles':
        return 0;
      case 'wizard':
        return 1;
      case 'jobs':
        return 2;
      default:
        return 1;
      }
    }
  },

  methods: {
    selectTab(tab) {
      if (this.$route.params.tab !== tab) {
        this.$router.push({path: `/tiktok/create-ads/${tab}`, params: {tab: tab}});
      }

      this.$store.dispatch('tiktokCreateAdsWizard/setSelectedBundle', null);
      this.$store.dispatch('tiktokCreateAdsWizard/setUploadBundle', null);
    }
  }
};
</script>