<template>
  <!--  -->
  <v-data-table
    :value="selectedAdAccounts"
    :headers="cols"
    :items="mainTableItems"
    :items-per-page="footerOptions.itemsPerPage"
    fixed-header
    :height="innerHeight - 216"
    show-select
    hide-default-footer
    :loading="mainTableLoading"
    @item-selected="itemSelected"
    @toggle-select-all="toggleSelectAll"
  >
    <!--  -->
    <template #[`item.name`]="{ item }">
      <h4 class="h5">
        {{ item.name }}
      </h4>
      <span class="caption">
        {{ item.accountName }}
      </span>
    </template>
    <!--   -->

    <!--  -->
    <template #[`item.options`]="{ item }">
      <main-table-options :ad-account="item" />
    </template>
    <!--   -->

    <!--  -->
    <template #[`item.link`]="{ item }">
      <main-table-link :ad-account="item" />
    </template>
    <!--   -->

    <!--  -->
    <template #[`item.budget`]="{ item }">
      <main-table-budget :ad-account="item" />
    </template>
    <!--   -->

    <!--  -->
    <template #footer>
      <v-data-footer
        show-first-last-page
        :options="footerOptions"
        :pagination="footerPagination"
        :items-per-page-options="[10, 30, 50, 100]"
        @update:options="updateFooterOptions"
      />
    </template>
    <!--   -->
  </v-data-table>
</template>

<script>
import footerDataMixin  from '@/mixins/tiktok/common/footer_data';
import { mapFields }    from 'vuex-map-fields';
import { mapGetters }   from 'vuex';

import MainTableBudget  from './TiktokWizardAdAccountsMainTableBudget.vue';
import MainTableLink    from './TiktokWizardAdAccountsMainTableLink.vue';
import MainTableOptions from './TiktokWizardAdAccountsMainTableOptions.vue';
 
export default {
  name: 'TiktokWizardAdAccountsMainTable',
  
  components: {
    MainTableOptions,
    MainTableLink,
    MainTableBudget
  },

  mixins: [ footerDataMixin ],

  computed: {
    ...mapGetters({
      innerHeight: 'main/innerHeight',
      adAccountOptions: 'tiktokCreateAdsWizard/adAccountOptions',
      mainTableData: 'tiktokCreateAdsWizard/mainTableData',
      mainTableLoading: 'tiktokCreateAdsWizard/mainTableLoading',
      mainTablePagination: 'tiktokCreateAdsWizard/mainTablePagination',
      selectedBundle: 'tiktokCreateAdsWizard/selectedBundle',
      filters: 'tiktokCreateAdsWizard/filters',
    }),
    ...mapFields('tiktokCreateAdsWizard', ['selectedAdAccounts']),

    cols() {
      const cols =  [
        { text: this.$t('common.name'), value: 'name' },
      ];
      
      if (this.selectedBundle && this.selectedBundle.objective !== 'REACH') {
        cols.push({ text: '', value: 'options', sortable: false, width: 250 });
        cols.push({ text: '', value: 'link', sortable: false, width: 250 });
      } else if (this.selectedBundle && this.selectedBundle.objective === 'TRAFFIC') {
        cols.push({ text: '', value: 'link', sortable: false, width: 250 });
      }

      cols.push({ text: '', value: 'budget', sortable: false, width: 250 });
      
      return cols;
    },

    mainTableItems() {
      if (this.filters.onlySelected) {
        return this.mainTableData.filter(i => {
          return this.selectedAdAccounts.findIndex(y => y.id === i.id) > -1;
        });
      }
      return this.mainTableData;
    }
  },

  created() {
    this.setMainTableData();
  },

  methods: {
    setMainTableData() {
      this.$store.dispatch('tiktokCreateAdsWizard/loadMainTableData', { endpoint: 'tiktok/adaccounts' });
    },
    
    async itemSelected({ item, value }) {
      const { id } = item;
      if (value) {
        if (this.adAccountOptions[id] === undefined) {
          await this.$store.dispatch('tiktokCreateAdsWizard/addAdAccountOption', { id, adAccount: item });
        }
        this.selectedAdAccounts.push(item);
      } else {
        this.selectedAdAccounts = this.selectedAdAccounts.filter(i => i.id !== id);
      }
    },

    toggleSelectAll({ items, value }) {
      for (const item of items) {
        this.itemSelected({ item, value });
      }
    },

    updateFooterOptions({ page, itemsPerPage }) {
      this.$store.dispatch('tiktokCreateAdsWizard/loadMainTableData', {
        endpoint: 'tiktok/adaccounts',
        page,
        itemsPerPage
      });
    }
  }
};
</script>
