<template>
  <div class="d-flex align-center justify-space-between mb-2">
    <div>
      <!--  -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selectedJobs.length === 0"
            color="primary"
            icon
            @click="bulkDelete"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash-alt
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>
      <!--   -->

      <!--  -->
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selectedJobs.length === 0"
            color="primary"
            icon
            :loading="loading"
            @click="restartJob"
            v-on="on"
          >
            <v-icon :size="16">
              replay
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.restart') }}</span>
      </v-tooltip>
      <!--   -->
    </div>
    
    <div />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokJobsTopbar',

  data() {
    return {
      loading: false
    };
  },

  computed: {
    ...mapGetters({
      selectedJobs: 'tiktokCreateAdsWizardJobs/selectedJobs',
    }),
  },

  methods: {
    async restartJob() {
      try {
        this.loading = true;

        for (const job of this.selectedJobs) {
          await this.api.post(`/tiktok/create-ads/jobs/${job.id}/restart`)
            .finally(() => {
              this.$store.dispatch('tiktokCreateAdsWizardJobs/editMainTableJob', {
                id: job.id,
                has_error: 0,
                success: 0,
                stage: 0,
              });
            });            
        }
      } catch (error) {
        console.log(error);
      } finally {
        this.loading = false;
      }
    },

    async bulkDelete() {
      this.$store.dispatch('tiktokCreateAdsWizardJobs/openDialog', 'bulkDelete');
    }
  },
};
</script>
