<template>
  <v-dialog
    :value="dialogs.adsetNameMacros"
    width="800"
    :transition="false"
    scrollable
    @keydown.esc="closeDialog"
    @click:outside="closeDialog"
  >
    <v-card>
      <v-card-title>{{ $t('common.availableMacrosForAdsets') }}</v-card-title>
      <v-card-text>
        <v-text-field
          v-model="adsetName"
          :label="$t('common.adsetName')"
          outlined
          dense
          hide-details
          class="my-2"
        />
        <div
          v-for="item in adsetMacros"
          :key="`adsets-macro-dialog-${item.macro}`"
        >
          <v-btn
            class="mb-1"
            x-small
            outlined
            :color="adsetName.includes(item.macro) ? 'primary' : 'grey'"
            @click="addMacro(item.macro)"
          >
            {{ item.macro }}
          </v-btn>
          <span>{{ ' - ' + $t(`upload.bundles.mainDialog.macros.${item.locale}`) }}</span>
        </div>
      </v-card-text>

      <v-divider />

      <v-card-actions>
        <v-spacer />
        <!--  -->
        <v-btn
          text
          color="grey"
          @click="closeDialog"
        >
          {{ $t('common.cancel') }}
        </v-btn>
        <!--   -->

        <!--  -->
        <v-btn
          color="primary"
          text
          @click="save"
        >
          {{ $t('common.save') }}
        </v-btn>
        <!--   -->
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { adsetMacros } from '@/constants/tiktok/upload/macros';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsDialogNameMacros',

  data() {
    return {
      adsetName: '',
      adsetMacros
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokCreateAds/dialogs',
      bundle: 'tiktokCreateAds/bundle'
    }),
  },

  created() {
    if (this.bundle.adsetName) {
      this.adsetName = this.bundle.adsetName;
    }
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokCreateAds/closeDialog', 'adsetNameMacros');
    },

    addMacro(macro) {
      if (this.adsetName.includes(macro)) {
        this.adsetName = this.adsetName.replace(macro, '');
      } else {
        this.adsetName += macro;
      }
    },

    save() {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'adsetName',
        value: this.adsetName
      });
      this.closeDialog();
    }
  }
};
</script>