<template>
  <div class="mx-2">
    <v-progress-linear
      :value="statusProgressPercent" 
      :color="statusProgressColor"
      :height="10"
      rounded
    />
  </div>
</template>

<script>
export default {
  name: 'TiktokJobsMainTableStatus',

  props: {
    job: {
      type: Object,
      required: true
    }
  },

  computed: {
    statusProgressPercent() {
      if (this.hasError === 1 || this.success === 1) {
        return 100;
      }
      return (this.stage / 4 * 100).toFixed();
    },

    statusProgressColor() {
      if (this.hasError === 1) { 
        return 'red';
      }
      if (this.success === 1) {
        return 'success';
      }
      return 'primary';
    },

    hasError() {
      return parseInt(this.job.has_error);
    },

    success() {
      return parseInt(this.job.success);
    },

    stage() {
      return parseInt(this.job.stage);
    }
  },
};
</script>