<template>
  <v-card>
    <v-card-text style="padding: 0px 8px 0px 8px">
      <!--  -->
      <v-data-table
        v-model="selected"
        :items="mainTableData"
        :items-per-page="footerOptions.itemsPerPage"
        :headers="cols"
        fixed-header
        :height="innerHeight - 50 - 48 - 36"
        show-select
        :loading="mainTableLoading"
        hide-default-footer
      >
        <!--    -->
        <template #[`item.wizard`]="{item}">
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn
                small
                color="primary"
                text
                icon
                style="min-width: 0; width: 30px; margin-top: 1px;"
                v-on="on"
                @click="$router.push({path: `/tiktok/create-ads/wizard?name=${item.name}`})"
              >
                <v-icon :size="18">
                  fas fa-cloud-upload-alt
                </v-icon>
              </v-btn>
            </template>
            <span>{{ $t('common.wizard') }}</span>
          </v-tooltip>
        </template>
        <!--     -->

        <!--  -->
        <template #[`item.name`]="{ item }">
          <span
            class="span-link"
            @click="edit(item)"
          >{{ item.name }}</span>
        </template>
        <!--   -->

        <!--  -->
        <template #footer>
          <v-data-footer
            show-first-last-page
            :options="footerOptions"
            :pagination="footerPagination"
            :items-per-page-options="[10, 30, 50, 100]"
            @update:options="updateFooterOptions"
          />
        </template>
        <!--   -->
      </v-data-table>
    </v-card-text>
  </v-card>
</template>

<script>
import footerDataMixin from '@/mixins/tiktok/common/footer_data';
import {mapFields}     from 'vuex-map-fields';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokBundlesMainTable',

  mixins: [ footerDataMixin ],

  computed: {
    ...mapGetters({
      innerHeight: 'main/innerHeight',
      mainTableData: 'tiktokCreateAds/mainTableData',
      mainTableLoading: 'tiktokCreateAds/mainTableLoading',
      mainTablePagination: 'tiktokCreateAds/mainTablePagination',
    }),
    ...mapFields('tiktokCreateAds', ['selected']),

    cols() {
      return [
        {
          value: 'wizard',
          width: 50,
        },
        {
          text: this.$t('common.name'),
          value: 'name',
        },
      ];
    },
  },

  methods: {
    async edit(bundle) {
      await this.$store.dispatch('tiktokCreateAds/setBundle', bundle);
      this.$store.dispatch('tiktokCreateAds/openDialog', 'mainDialog');
    },

    updateFooterOptions({ page, itemsPerPage }) {
      this.$store.dispatch('tiktokCreateAds/loadMainTableData', {
        endpoint: 'tiktok/bundles',
        page,
        itemsPerPage
      });
    }
  }
};
</script>
