<template>
  <div
    v-if="selectedBundle"
    class="pa-2"
  >
    <v-text-field
      v-if="adAccountSelected"
      :value="adAccountOptions[adAccount.id].budget"
      :suffix="adAccount.currency ? adAccount.currency : 'USD'"
      type="number"
      min="0"
      :label="$t('common.budget')"
      outlined
      dense
      :error-messages="hasSpecificInvalid('adAccountOptions', adAccount.id, 'budget') ? $t('common.required') : null"
      hide-details="auto"
      class="centralize-append-inner"
      @input="setOptionValue(adAccount.id, 'budget', $event)"
      @blur="blurBudget"
    >
      <template #append>
        <v-btn
          v-if="selectedBundle.budgetRandomize"
          icon
          small
          @click="randomizeBudget"
        >
          <v-icon
            :size="16"
          >
            fas fa-sync-alt
          </v-icon>
        </v-btn>
      </template>
    </v-text-field>
    <v-skeleton-loader
      v-else
      boilerplate
      type="text"
      class="tiktok-upload-wizard-skeleton"
    />
  </div>
</template>

<script>
import { 
  randomizeBudgetFunc,
  getConvertedMinBudget } from '@/mixins/budget_mixin';
import validationMixin from '@/mixins/tiktok/common/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokWizardAdAccountsMainTableBudget',

  mixins: [ validationMixin ],

  props: {
    adAccount: {
      type: Object,
      required: true
    }
  },

  computed: {
    ...mapGetters({
      selectedAdAccounts: 'tiktokCreateAdsWizard/selectedAdAccounts',
      adAccountOptions: 'tiktokCreateAdsWizard/adAccountOptions',
      selectedBundle: 'tiktokCreateAdsWizard/selectedBundle',
      $validation: 'tiktokCreateAdsWizard/$validation',
    }),

    adAccountSelected() {
      if (this.selectedAdAccounts.length === 0) return false;
      return this.selectedAdAccounts.findIndex(item => item.id === this.adAccount.id) > -1;
    }
  },

  methods: {
    setOptionValue(id, key, value) {
      this.$store.dispatch('tiktokCreateAdsWizard/setAdAccountOptionProperty', { id, key, value });
    },

    blurBudget() {
      let usdAmmount = 50;
      if (this.selectedBundle.budgetLevel === 'adset') {
        usdAmmount = 20;
      } 
      const min = getConvertedMinBudget(usdAmmount, this.adAccount.currency);

      if (this.adAccountOptions[this.adAccount.id].budget <= min) {
        this.setOptionValue(this.adAccount.id, 'budget', min);
      }
    },

    randomizeBudget() {
      const { 
        budget,
        budgetRandomValue,
        budgetLevel } = this.selectedBundle;

      const randomizedValue = randomizeBudgetFunc(budget, budgetRandomValue, budgetLevel, this.adAccount.currency);
      this.setOptionValue(this.adAccount.id, 'budget', randomizedValue);
    },
  }
};
</script>