<template>
  <div class="pa-2">
    <v-textarea
      :value="text"
      :label="$t('common.text')"
      outlined
      dense
      :rules="rules"
      :error-messages="hasSpecificInvalid('adAccountOptions', adAccount.id, 'text') ? rules : null"
      hide-details="auto"
      @input="setOptionValue(adAccount.id, 'text', $event)"
    />
  </div>
</template>

<script>
import validationMixin from '@/mixins/tiktok/common/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokWizardCreativesMainTableText',

  mixins: [ validationMixin ],

  props: {
    adAccount: {
      type: Object,
      required: true
    }
  },

  computed: {
    ...mapGetters({
      adAccountOptions: 'tiktokCreateAdsWizard/adAccountOptions',
      $validation: 'tiktokCreateAdsWizard/$validation',
    }),
    
    rules() {
      if (this.text.length === 0) {
        return [this.$t('common.required')];
      }
      return  [
        value => (value).length <= 100 || this.$t('common.maxLength', { count: 100 }),
      ];
    },

    text() {
      return this.adAccountOptions[this.adAccount.id].text;
    }
  },

  created() {
    if (this.text && this.text.match(/{(.+?)}/)) {
      this.initSpintax();
    }
  },

  methods: {
    setOptionValue(id, key, value) {
      this.$store.dispatch('tiktokCreateAdsWizard/setAdAccountOptionProperty', { id, key, value });
    },

    async initSpintax() {
      const response = await this.api.post('/tiktok/create-ads/spintax', {
        text: this.text
      });

      const { status, data } = response;
      if (status === 200 && data && data.data && data.data.text) {
        this.setOptionValue(this.adAccount.id, 'text', data.data.text);
      }
    }
  }
};
</script>
