<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.placements.placements') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <v-radio-group
          v-if="bundle.objective !== 'REACH'"
          :value="bundle.placement"
          class="ml-2 mt-0"
          hide-details
          @change="setPlacement"
        >
          <v-radio
            v-for="(placement, placementIndex) in ['auto', 'manual']"
            :key="`create-ads-main-dialog-demograpy-placement-${placement}-${placementIndex}`"
            :label="$t(`upload.placements.${placement}Placement`)"
            :value="placement"
          />
        </v-radio-group>

        <!--   -->
        <v-list v-if="bundle.placement === 'manual'">
          <v-list-item-group
            :value="bundle.placementsManual"
            multiple
            @change="setPlacementsManual"
          >
            <v-list-item
              v-for="(placement, placementIndex) in manualPlacements"
              :key="`create-ads-main-dialog-demograpy-manual-placement-${placement}-${placementIndex}`"
              :value="placement"
            >
              <template v-slot:default="{ active }">
                <v-list-item-action>
                  <v-checkbox
                    :input-value="active"
                    color="primary"
                  />
                </v-list-item-action>

                <v-list-item-avatar>
                  <v-img :src="getImage(placement)" />
                </v-list-item-avatar>

                <v-list-item-content class="ml-2">
                  <v-list-item-title>
                    {{ $t(`upload.placements.${placement}`) }}
                  </v-list-item-title>
                </v-list-item-content>
              </template>
            </v-list-item>
          </v-list-item-group>
        </v-list>
        <!--    -->

        <!--   -->
        <v-switch
          :input-value="bundle.placementUserComment"
          :label="$t('upload.placements.userComment')"
          dense
          hide-details
          class="pt-0"
          :class="{ 'mt-0': bundle.objective === 'REACH',
                    'mt-3': bundle.objective !== 'REACH' }"
          @change="setPlacementUserComment"
        />
        <!--    -->

        <!--   -->
        <v-switch
          :input-value="bundle.placementVideoDownload"
          :label="$t('upload.placements.videoDownload')"
          dense
          hide-details
          class="mt-3 pt-0"
          @change="setPlacementVideoDownload"
        />
        <!--    -->
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsPlacements',

  data() {
    return {
      manualPlacements: [
        'tiktok',
        'newsFeedAppSeries',
        'pangle'
      ]
    };
  },

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle'
    })
  },

  methods: {
    getImage(value) {
      switch (value) {
      case 'tiktok':
        return require('@/assets/images/placement-tt.png');
      case 'newsFeedAppSeries':
        return require('@/assets/images/placement-tb.png');
      case 'pangle':
        return require('@/assets/images/placement-pangle.png');
      }
    },
    
    setPlacement(placement) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'placement',
        value: placement
      });
    },
    
    setPlacementUserComment(value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'placementUserComment',
        value: value
      });
      localStorage.setItem('dolphinTiktokBundlePlacementUserComment', value);
    },

    setPlacementVideoDownload(value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'placementVideoDownload',
        value: value
      });
      localStorage.setItem('dolphinTiktokBundlePlacementVideoDownload', value);
    },

    setPlacementsManual(value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', {
        key: 'placementsManual',
        value: value
      });
    }
  }
};
</script>