<template>
  <v-stepper
    :value="bundle.step"
    class="create-ads-main-dialog-stepper"
    @change="setValue('step', $event)"
  >
    <v-stepper-header class="create-ads-main-dialog-stepper-header">
      <v-stepper-step
        v-for="(step, stepIndex) in ['campaigns', 'adsets', 'ads']"
        :key="`tiktok-main-dialog-stepper-${step}-${stepIndex}`"
        :step="stepIndex + 1"
        :complete="bundle.step > (stepIndex + 1)"
        :editable="isEditable(step)"
        class="py-0"
      >
        {{ $t(`adsmanager.tabs.${step}`) }}
      </v-stepper-step>
    </v-stepper-header>

    <v-stepper-items class="create-ads-main-dialog-stepper-items">
      <!--  -->
      <campaigns />

      <!--  -->
      <adsets />
      
      <!--  -->
      <ads />
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import { mapGetters } from 'vuex';
 
import Ads            from './ads/TiktokCreateAdsMainDialogStepperAds.vue';
import Adsets         from './adsets/TiktokCreateAdsMainDialogStepperAdsets.vue';
import Campaigns      from './campaigns/TiktokCreateAdsMainDialogStepperCampaigns.vue';

export default {
  name: 'TiktokCreateAdsMainDialogStepper',

  components: {
    Campaigns,
    Adsets,
    Ads
  },

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle'
    }),
  },

  methods: {
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },

    isEditable(step) {
      if (step === 'campaigns') {
        return true;
      } else if (step === 'adsets' && this.bundle.campaignsStepValid) {
        return true;
      } else if (step === 'ads' && this.bundle.adsetsStepValid) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>