<template>
  <div class="pa-2">
    <v-carousel
      v-if="hasCreatives"
      height="150px"
      next-icon="keyboard_arrow_right"
      prev-icon="keyboard_arrow_left"
      class="tiktok-wizard-creatives-carousel"
    >
      <v-carousel-item
        v-for="creative in creatives"
        :key="creative.filename"
      > 
        <v-row
          class="flex-nowrap"
          style="height: 100%"
        > 
          <v-col class="text-center pa-0"> 
            <div class="wizard-creatives-main-table-video-wrapper">
              <video
                :src="creative.url.replace('http://127.0.0.1:3056', 'https://cloud.dolphin.ru.com')" 
                muted
                loop
                class="wizard-creatives-main-table-video"
                @mouseover="hoverCreative = creative.filename"
              />
              <div
                v-show="showOverlay(creative.filename)"
                class="wizard-creatives-main-table-video-overlay"
                @mouseleave="hoverCreative = null"
              >
                <v-chip
                  color="red"
                  text
                  small
                  @click="removeCreative(creative.filename)"
                >
                  {{ $t('common.delete') }}
                </v-chip>
              </div>
            </div>
          </v-col>  
        </v-row> 
      </v-carousel-item>
    </v-carousel>
    
    <!--    -->
    <v-col class="text-center">
      <v-btn
        text
        :color="showCreativeError ? 'red' : 'primary'"
        small
        :loading="sendingCreative"
        @click="openFileUploader"
      >
        <v-icon
          :size="18"
          class="mr-1"
        >
          add
        </v-icon>
        {{ $t('upload.wizard.static.addCreatives') }}
      </v-btn>
      <v-alert
        v-if="showCreativeError"
        text
        color="red"
        class="mb-0 mx-auto mt-2"
        dense
        max-width="400px"
      >
        {{ $t('validation.min', { length: 1 }) }}
      </v-alert>
      <!--     -->

      <!--    -->
      <input
        v-show="false"
        ref="inputUpload"
        type="file"
        multiple
        @change="handleInputUpload"
      >

      <!--     -->
    </v-col>
  </div>
</template>

<script>
import validationMixin from '@/mixins/tiktok/common/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokWizardCreativesMainTableCreatives',

  mixins: [ validationMixin ],

  props: {
    adAccount: {
      type: Object,
      required: true
    }
  },

  data() {
    return {
      sendingCreative: false,
      hoverCreative: null
    };
  },

  computed: {
    ...mapGetters({
      adAccountOptions: 'tiktokCreateAdsWizard/adAccountOptions',
      $validation: 'tiktokCreateAdsWizard/$validation',
    }),
    
    columns() {
      if (this.$vuetify.breakpoint.xl) return 4;
      if (this.$vuetify.breakpoint.lg) return 3;
      if (this.$vuetify.breakpoint.md) return 2;
      return 1;
    },

    hasCreatives() {
      return !!this.adAccountOptions[this.adAccount.id].creatives.length;
    },

    creatives() {
      if (this.hasCreatives) {
        return this.adAccountOptions[this.adAccount.id].creatives;
      }
      return [];
    },

    showCreativeError() {
      return this.hasSpecificInvalid('adAccountOptions', this.adAccount.id, 'creatives');
    }
  },

  methods: {
    openFileUploader () {
      this.$refs.inputUpload.value = '';
      this.$refs.inputUpload.click();
    },

    async handleInputUpload () {
      try {
        this.sendingCreative = true;

        for (const file of this.$refs.inputUpload.files) {
          const { filename, url} = await this.sendCreativeToServer(file);

          if (filename && url) {
            const creative = {
              original_filename: file.name,
              url: url,
              type: file.type,
              size: file.size,
              filename: filename
            };
            this.setCreativeValue(creative);
          }
        }
      } catch (error) {
        console.log(error);
      } finally {
        this.sendingCreative = false;
      }
    },

    async sendCreativeToServer(file) {
      try {
        let formData = new FormData();
        formData.append('file', file);
        const options = {
          headers: { 'Content-Type': 'multipart/form-data' }
        };
        const response = await this.api.post('tiktok/create-ads/upload-media', formData, options);
        const { status, data } = response;

        if (status === 200 && data && data.data) {
          return data.data;
        } else {
          throw new Error('invalid response');
        }
      } catch (error) {
        console.log(error);
        this.$store.dispatch('main/apiError',JSON.stringify(error.response.data.errors));
      }
    },

    setCreativeValue(creative) {
      this.$store.dispatch('tiktokCreateAdsWizard/addAdAccountOptionCreo', {
        id: this.adAccount.id,
        value: creative
      });
    },

    removeCreative(creativeName) {
      this.$store.dispatch('tiktokCreateAdsWizard/removeCreative', {
        id: this.adAccount.id,
        creativeName: creativeName
      });
    },

    showOverlay(creativeName) {
      if (this.hoverCreative === creativeName) {
        return true;
      }
      return false;
    }
  }
};
</script>

