  position: relative;
  max-height: 150px;
  width: min-content;
  margin-left: auto;
  margin-right: auto;
}
.wizard-creatives-main-table-video {
  max-width: 150px;
  max-height: 150px;
  width: auto;
  height: 100%;
  cursor: pointer;
}
.wizard-creatives-main-table-video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
