<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.bundles.mainDialog.cardsTitles.geo') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <v-alert
          v-if="hasValidationInvalid('location')"
          border="left"
          color="red"
          text
          type="error"
        >
          {{ $t('validation.min', { length: 1 }) }}
        </v-alert>

        <!--  -->
        <v-row>
          <v-col cols="12">
            <v-text-field
              :value="searchValue"
              :label="$t('common.search')"
              outlined
              dense
              hide-details
              clearable
              class="mb-1"
              @input="handleSearchInput"
            />
          </v-col>
          <v-col
            cols="9"
            style="max-height: 358px; overflow-x: auto;"
          >
            <v-list
              subheader
              two-line
              flat
            >
              <v-list-item-group
                :value="bundle.location"
                multiple
                @change="setLocation"
              >
                <v-list-item
                  v-for="area in currentLocaleAreas"
                  :key="`create-ads-main-dialog-adset-geo-${area.id}`"
                  :value="area"
                >
                  <template v-slot:default="{ active }">
                    <v-list-item-action>
                      <v-checkbox
                        :input-value="active"
                        color="primary"
                      />
                    </v-list-item-action>

                    <v-list-item-content>
                      <v-list-item-title>{{ area.translation }}</v-list-item-title>
                      <v-list-item-subtitle>{{ area.description }}</v-list-item-subtitle>
                    </v-list-item-content>
                  </template>
                </v-list-item>
              </v-list-item-group>
            </v-list>
            <!--   -->
          </v-col>

          <v-col
            cols="3"
            style="max-height: 358px; overflow-x: auto;"
          >
            <v-btn
              color="primary"
              text
              x-small
              block
              @click="setValue('location', currentLocaleAreas)"
            >
              {{ $t('common.add') }} {{ $t('common.all').toLowerCase() }}
            </v-btn>
            <v-btn
              color="red"
              text
              x-small
              class="mt-2"
              block
              @click="setValue('location', [])"
            >
              {{ $t('common.clear') }} {{ $t('common.all').toLowerCase() }}
            </v-btn>
            <div
              v-if="allLocationsAreSelected" 
              style="color: #4CAF50;"
              class="mt-2 subtitle-2 text-center"
            >
              {{ $t('createAds.mainDialog.allLocationsAreSelected') }}
            </div>
            <!--   -->
            <div v-if="showLocationChips">
              <v-chip
                v-for="(item, index) in bundle.location"
                :key="`create-ads-main-dialog-geo-location-chip-${item}-${index}`"
                class="mt-1 ml-1"
                color="success"
                label
                x-small
                outlined
              >
                {{ item.translation }}
              </v-chip>
            </div>
            <!--    -->
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import areasCn         from '@/constants/tiktok/upload/areas_cn';
import areasEn         from '@/constants/tiktok/upload/areas_en';
import areasRu         from '@/constants/tiktok/upload/areas_ru';
import validationMixin from '@/mixins/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsGeo',
  
  mixins: [ validationMixin ],

  data() {
    return {
      expansionPanel: [0],
      searchValue: '',
      searchQuery: '',
      forAllLocations: [],
      pangleLocationIds: [
        3865483,
        1605651,
        3996063,
        357994,
        1831722,
        298795,
        1522867,
        1733045,
        1694008,
        1880251,
        1668284,
        630336,
        1835841,
        1861060,
        1562822,
        3932488,
        3895114,
        1643084,
        102358,
        2017370,
        3686110,
        6251999,
        690791,
        3469034,
        294640,
        1269750,
        290557
      ],
      newsFeedAppSeriesLocationIds: [
        1668284,
        1643084,
        1269750,
        1861060
      ],
      tiktokLocationIds: [
        2264397,
        3469034,
        298795,
        719819,
        290291,
        289688,
        1880251,
        2635167,
        1269750,
        2963597,
        1562822,
        2186224,
        953987,
        3996063,
        1605651,
        11835841,
        2017370,
        690791,
        1643084,
        390903,
        2623032,
        3175395,
        357994,
        798544,
        2510769,
        3017382,
        1861060,
        102358,
        1831722,
        2077456,
        660013,
        2802361,
        285570,
        3686110,
        1168579,
        1668284,
        1733045,
        286963,
        2661886,
        630336,
        798549,
        2542007,
        6252001,
        2921044,
        2782113,
        3895114,
        1522867,
        3865483,
        290557,
        6251999,
        3932488,
        3144096,
        2658434,
        2750405,
        1694008,
        3077311,
        294640
      ],
    };
  },

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle',
      profile: 'main/profile'
    }),

    currentLocaleAreas() {
      let items = areasRu;
      let pangleItems = [];
      let newsFeedItems = [];
      let tiktokItems = [];

      if (this.profile.language === 'en') {
        items = areasEn;
      } else if (this.profile.language === 'cn') {
        items = areasCn;
      }

      const { placement, placementsManual } = this.bundle;
      if (placement === 'manual' && placementsManual.includes('pangle')) {
        pangleItems = items.filter(i => this.pangleLocationIds.includes(parseInt(i.id, 10)));
      }
      if (placement === 'manual' && placementsManual.includes('newsFeedAppSeries')) {
        newsFeedItems = items.filter(i => this.newsFeedAppSeriesLocationIds.includes(parseInt(i.id, 10)));
      }
      if (placement === 'manual' && placementsManual.includes('tiktok')) {
        tiktokItems = items.filter(i => this.tiktokLocationIds.includes(parseInt(i.id, 10)));
      }

      if (pangleItems.length || newsFeedItems.length || tiktokItems.length) {
        items = [...new Set(pangleItems.concat(newsFeedItems, tiktokItems))];
      }

      if (this.searchQuery) {
        return items.filter(item => {
          const { location } = this.bundle;
          if (Array.isArray(location) && location.length) {
            return location.findIndex(i => i.id === item.id) > -1 ||
              item.translation.toString().toLowerCase().includes(this.searchQuery.toString().toLowerCase());
          }

          return item.translation.toString().toLowerCase().includes(this.searchQuery.toString().toLowerCase());
        });
      }

      return items;
    },

    allLocationsAreSelected() {
      return this.bundle.location.length >= 1737;
    },

    showLocationChips() {
      return Array.isArray(this.bundle.location) &&
        !this.allLocationsAreSelected &&
        this.bundle.location.length;
    }
  },

  created() {
    if (Array.isArray(this.bundle.location) && this.bundle.location.length) {
      const locations = [];

      for (const location of this.bundle.location) {
        for (const area of this.currentLocaleAreas) {
          if (parseInt(location.id, 10) === parseInt(area.id, 10)) {
            locations.push(area);
          }
        }
      }

      this.setLocation(locations);
    }
  },

  methods: {
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },

    setLocation(value) {
      this.setValue('location', value);
    },

    addAllLocations(locations) {
      for (const location of locations) {
        if (Array.isArray(location.children) && location.children.length) {
          this.addAllLocations(location.children);
        } else {
          this.forAllLocations.push(location);
        }
      }

      this.setLocation(this.forAllLocations);
    },

    handleSearchInput(value) {
      this.searchValue = value;
      setTimeout(async () => {
        if (value === this.searchValue) {
          this.searchQuery = value;
        }
      }, 500);
    },

    handleLocationFilter(item, search) {
      const { translation, description } = item;
      return this.findMatch(search, translation, description);
    },

    findMatch (query, translation, description) {
      query = query.toString().toLowerCase();
      translation = translation.toString().toLowerCase();
      description = description.toString().toLowerCase();

      return translation.includes(query) || description.includes(query);
    }
  }
};
</script>