<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.wizard.optimizationGoal') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <!--   -->
        <v-autocomplete
          :value="bundle.optimizationGoal"
          :label="$t('upload.wizard.optimizationGoal')"
          :items="optimizationGoals"
          outlined
          dense
          hide-details
          class="mt-0"
          @change="setValue('optimizationGoal', $event)"
        />
        <!--    -->
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsOptimization',

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle',
    }),

    optimizationGoals() {
      return [
        { text: this.$t('common.appInstalls'), value: 'install', },
        { text: this.$t('common.appEvents'), value: 'in_app_event', }
      ];
    }
  },

  methods: {
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    }
  }
};
</script>