<template>
  <v-col
    cols="12"
    md="6"
    offset-md="3"
    xl="4"
    offset-xl="4"
  >
    <v-card>
      <!--   -->
      <v-card-title>
        {{ $t('upload.bundles.mainDialog.cardsTitles.detailedTargeting') }}
      </v-card-title>
      <!--    -->

      <v-card-text>
        <!--  -->
        <v-autocomplete
          ref="bundleLocaleAutocomplete"
          :value="bundle.locale"
          :label="$t('common.languages')"
          :item-text="item => item.label" 
          :item-value="item => item.code"
          :items="currentLocaleItems"
          outlined
          dense
          hide-details
          class="mt-0"
          multiple
          chips
          small-chips
          deletable-chips
          clearable
          @change="setLocale"
        />
        <!--   -->
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import { ru, en, cn } from '@/constants/tiktok/upload/locales';
import { mapGetters } from 'vuex';

export default {
  name: 'TiktokCreateAdsMainDialogStepperAdsetsDetailedTargeting',

  computed: {
    ...mapGetters({
      bundle: 'tiktokCreateAds/bundle',
      profile: 'main/profile'
    }),

    currentLocaleItems() {
      switch(this.profile.language) {
      case 'en':
        return en;
      case 'cn':
        return cn;
      default:
        return ru;
      }
    }
  },

  methods: {
    setValue(key, value) {
      this.$store.dispatch('tiktokCreateAds/setBundleProperty', { key, value });
    },

    setLocale(value) {
      if (this.$refs.bundleLocaleAutocomplete) {
        this.$refs.bundleLocaleAutocomplete.internalSearch = '';
        this.$refs.bundleLocaleAutocomplete.blur();
      }

      const isEmpty = value === null || value.length === 0;
      const lastItem = Array.isArray(value) && value.length ? value[value.length - 1] : null;

      if (isEmpty || lastItem === 'all') {
        this.setValue('locale', ['all']);
      } else {
        this.setValue('locale', value.filter(i => i !== 'all'));
      }
    }
  }
};
</script>