<template>
  <div class="create-ads-content">
    <main-dialog v-if="dialogs.mainDialog" />
    <tabs />
    <div class="create-ads-tab-content">
      <component :is="componentName" />
    </div>
  </div>
</template>

<script>
import Tabs           from '@/components/tiktok/createAds/TiktokCreateAdsTabs.vue';
import MainDialog     from '@/components/tiktok/createAds/mainDialog/TiktokCreateAdsMainDialog.vue';
import { mapGetters } from 'vuex';

import Bundles        from './TiktokCreateAdsBundles.vue';
import Jobs           from './TiktokCreateAdsJobs.vue';
import Wizard         from './TiktokCreateAdsWizard.vue';

export default {
  name: 'TiktokCreateAds',

  components: {
    Tabs,
    Bundles,
    Jobs,
    Wizard,
    MainDialog
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokCreateAds/dialogs'
    }),

    componentName() {
      if (typeof this.$route.params.tab !== 'undefined') {
        return this.$route.params.tab;
      }
      return 'wizard';
    }
  },

  created() {
    this.$store.dispatch('tiktokCreateAds/loadMainTableData', { endpoint: 'tiktok/bundles' });
  }
};
</script>
