<template>
  <div
    v-if="selectedBundle"
    class="pa-2"
  >
    <!--     -->
    <div v-if="selectedBundle.objective === 'APP_INSTALLS'">
      <v-autocomplete
        v-if="adAccountSelected"
        :value="adAccountOptions[adAccount.id].app"
        :label="$t('createAds.wizard.adAccounts.selectApp')"
        :item-text="item => item.name"
        :item-value="item => item.id"
        :items="adAccountApps"
        return-object
        outlined
        dense
        :error-messages="hasSpecificInvalid('adAccountOptions', adAccount.id, 'app') ? $t('common.required') : null"
        hide-details="auto"
        @input="setApp"
      >
        <template #[`item`]="{ item }">
          <template>
            <v-list-item-avatar v-if="getIconUri(item)">
              <img :src="`https://lf16-adcdn-sg.ibytedtos.com/obj/${getIconUri(item)}`">
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title v-html="item.name" />
              <v-list-item-subtitle v-html="item.group" />
            </v-list-item-content>
          </template>
        </template>
      </v-autocomplete>
      <v-skeleton-loader
        v-else
        boilerplate
        type="text"
        class="tiktok-upload-wizard-skeleton"
      />

      <v-autocomplete
        v-if="showAppEvents"
        :value="adAccountOptions[adAccount.id].appEvent"
        :label="$t('common.appEvents')"
        :item-text="item => item.name"
        :item-value="item => item.value"
        :items="appEvents"
        outlined
        dense
        :error-messages="hasSpecificInvalid('adAccountOptions', adAccount.id, 'appEvent') ? $t('common.required') : null"
        hide-details="auto"
        class="mt-2"
        :loading="loading.appEvents"
        @input="setAppEvent"
      />
    </div>
    <!--      -->

    <!--    -->
    <div v-if="selectedBundle.objective === 'CONVERSIONS'">
      <!--   -->
      <v-autocomplete
        v-if="adAccountSelected"
        :value="adAccountOptions[adAccount.id].pixel"
        :label="$t('createAds.wizard.adAccounts.selectPixel')"
        outlined
        dense
        :error-messages="hasSpecificInvalid('adAccountOptions', adAccount.id, 'pixel') ? $t('common.required') : null"
        hide-details="auto"
        :item-text="item => item.name"
        :item-value="item => item.id"
        :items="adAccountPixels"
        return-object
        @input="setOptionValue(adAccount.id, 'pixel', $event)"
      >
        <template #[`item`]="{ item }">
          <div class="d-flex flex-column">
            <div>
              <v-badge
                dot
                :color="getBadgeColor(item.activity_status)"
              >
                <h4 class="h5">
                  {{ item.name }}
                </h4>
              </v-badge>
            </div>
            <div class="caption">
              {{ `ID: ${item.pixel_code}` }}
            </div>
          </div>
        </template>
      </v-autocomplete>
      <v-skeleton-loader
        v-else
        boilerplate
        type="text"
        class="tiktok-upload-wizard-skeleton"
      />
      <!--    -->

      <!--    -->
      <v-autocomplete
        v-if="adAccountSelected"
        :value="adAccountOptions[adAccount.id].pixelEvent"
        :label="$t('createAds.wizard.adAccounts.selectPixelEvent')"
        :item-text="item => item.name"
        :item-value="item => item.id"
        :items="adAccountPixelEvents"
        return-object
        outlined
        dense
        :error-messages="hasSpecificInvalid('adAccountOptions', adAccount.id, 'pixelEvent') ? $t('common.required') : null"
        hide-details="auto"
        class="mt-2"
        @input="setOptionValue(adAccount.id, 'pixelEvent', $event)"
      />
      <v-skeleton-loader
        v-else
        boilerplate
        type="text"
        class="tiktok-upload-wizard-skeleton mt-2"
      />
      <!--    -->
    </div>
    <!--     -->
  </div>
</template>

<script>
import validationMixin from '@/mixins/tiktok/common/validation_mixin';
import { mapGetters }  from 'vuex';

export default {
  name: 'TiktokWizardAdAccountsMainTableOptions',

  mixins: [ validationMixin ],

  props: {
    adAccount: {
      type: Object,
      required: true
    }
  },

  data() {
    return {
      items: [ 'some', 'new', 'someNew' ],
      appEvents: [],
      loading: {
        appEvents: false
      },
      emptyEvents: false
    };
  },

  computed: {
    ...mapGetters({
      selectedAdAccounts: 'tiktokCreateAdsWizard/selectedAdAccounts',
      adAccountOptions: 'tiktokCreateAdsWizard/adAccountOptions',
      selectedBundle: 'tiktokCreateAdsWizard/selectedBundle',
      $validation: 'tiktokCreateAdsWizard/$validation',
      allUserApps: 'tiktokUserApps/all',
    }),

    adAccountSelected() {
      if (this.selectedAdAccounts.length === 0) return false;
      return this.selectedAdAccounts.findIndex(item => item.id === this.adAccount.id) > -1;
    },

    adAccountApps() {
      let items = [];

      if (this.adAccount && Array.isArray(this.adAccount.apps)) {
        const header = [{ header: this.$t('common.userApp.adAccountApps') }];
        items = items.concat(header, this.adAccount.apps);
      }

      if (Array.isArray(this.allUserApps) && this.allUserApps.length) {
        const header = [
          { divider: true },
          { header: this.$t('common.userApp.userApps') }
        ];
        items = items.concat(header, this.allUserApps);
      }

      return items;
    },

    adAccountPixels() {
      if (this.adAccount && Array.isArray(this.adAccount.pixels)) {
        return this.adAccount.pixels;
      }

      return [];
    },

    adAccountPixelEvents() {
      const { pixel } = this.adAccountOptions[this.adAccount.id];
      if (pixel && Array.isArray(pixel.events)) {
        return pixel.events;
      }
      return [];
    },

    showAppEvents() {
      return this.selectedBundle.optimizationGoal === 'in_app_event' &&
          this.adAccountSelected &&
          this.adAccountOptions[this.adAccount.id].app;
    }
  },

  watch: {
    selectedAdAccounts() {
      if (this.showAppEvents &&
          !this.appEvents.length &&
          !this.emptyEvents)
      {
        const { id } = this.adAccountOptions[this.adAccount.id].app;
        this.loadAppEvents(id);
      }
    }
  },

  methods: {
    setOptionValue(id, key, value) {
      this.$store.dispatch('tiktokCreateAdsWizard/setAdAccountOptionProperty', { id, key, value });
    },

    getBadgeColor(activityStatus) {
      switch (parseInt(activityStatus, 10)) {
      case 1:
        return 'orange';
      case 2:
        return 'success';
      }
    },

    getIconUri(item) {
      if (item.icon) {
        if (item.icon.web_uri) {
          return item.icon.web_uri;
        }
        // try {
        //   const iconObj = JSON.parse(item.icon);
        //   if (iconObj && iconObj.web_uri) {
        //     return iconObj.web_uri;
        //   }
        // } catch (error) {
        //   // 
        // }
        return item.icon;
      }
    },

    setApp (value) {
      this.setOptionValue(this.adAccount.id, 'app', value);
      this.loadAppEvents(value.id);
    },

    setAppEvent (value) {
      this.setOptionValue(this.adAccount.id, 'appEvent', value);
    },

    async loadAppEvents(id) {
      try {
        this.emptyEvents = false;
        this.loading.appEvents = true;

        const response = await this.api(`/tiktok/app/${id}/events`);
        const { status, data } = response;
        if (status === 200 && data && Array.isArray(data.data)) {
          this.appEvents = [];
          for (const i of data.data) {
            this.appEvents.push({
              id: i.app_id,
              name: i.name,
              value: i.key
            });
          }
        }
      } catch (error) {
        //
      } finally {
        this.loading.appEvents = false;
        this.emptyEvents = true;
      }
    }
  }
};
</script>
